import { useState } from 'react';
import { useNegativeFeedbackUrl } from '../../hooks/useNegativeFeedbackUrl';
import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';
import { setLocalStorageData } from '@app/storage/local/shared';
import { useFeedbackOverlay } from '@popup/shared/contexts/FeedbackOverlayContext';
import { FeedbackType } from '@popup/project/youboost/components/shared/popups/Feedback/types';
import { PopupWrapper } from '@popup/project/youboost/components/shared/PopupWrapper/PopupWrapper';
import { DefaultFooter } from '@popup/project/youboost/components/shared/popups/Feedback/ui/DefaultFooter/DefaultFooter';
import { NegativeFeedbackFooter } from '@popup/project/youboost/components/shared/popups/Feedback/ui/NegativeFeedbackFooter/NegativeFeedbackFooter';
import { PositiveFeedbackFooter } from '@popup/project/youboost/components/shared/popups/Feedback/ui/PositiveFeedbackFooter/PositiveFeedbackFooter';

export const Feedback = () => {
  const { t } = useTranslation('youboost', { keyPrefix: 'root.feedback' });
  const [feedbackState, setFeedbackState] = useState<FeedbackType>();
  const { isFeedbackOverlayShown, closeFeedbackOverlay } = useFeedbackOverlay();
  const { negativeFeedbackUrl } = useNegativeFeedbackUrl();

  const actionClickHandler = async (state: FeedbackType) => {
    setFeedbackState(state);
    await setLocalStorageData({ feedbackProvided: true });
  };

  const handleClickReview = async (isPositive: boolean) => {
    const url = isPositive
      ? process.env.POPUP_HTML_PUBLIC_FEEDBACK_URL
      : negativeFeedbackUrl;
    if (url) {
      await browser_api.tabs.create({ url });
    }
    closeFeedbackOverlay();
  };

  if (!isFeedbackOverlayShown) return null;

  return (
    <PopupWrapper
      footer={
        typeof feedbackState === 'undefined' ? (
          <DefaultFooter actionClickHandler={actionClickHandler} />
        ) : feedbackState === 'positive' ? (
          <PositiveFeedbackFooter
            clickHandler={() => handleClickReview(true)}
          />
        ) : (
          <NegativeFeedbackFooter
            clickHandler={() => handleClickReview(false)}
          />
        )
      }
      onClose={closeFeedbackOverlay}
    >
      <h3>
        {t(
          typeof feedbackState === 'undefined'
            ? 'default.title'
            : feedbackState === 'positive'
              ? 'positive.title'
              : 'negative.title',
        )}
      </h3>
    </PopupWrapper>
  );
};
