import { FC } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { CopyButton } from '@popup/project/youboost/components/shared/CopyButton';
import { Copy } from '@popup/project/youboost/components/shared/icons';
import s from './Footer.module.css';

interface Props {
  supportUrl: string;
}

export const Footer: FC<Props> = ({ supportUrl }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'support-page' });
  const navigate = useNavigate();

  const [userId] = useLocalStorage([LocalStorageKey.UserId]);
  const [deviceId] = useLocalStorage([LocalStorageKey.DeviceId]);

  const goToSupportHandler = async () => {
    await browser_api.tabs.create({ url: supportUrl });
  };
  const closeHandler = async () => {
    navigate(RoutePath.Root);
  };

  const version = browser_api.runtime.getManifest().version;

  return (
    <div className={s.box} data-testid={'support-footer'}>
      <Button
        onClick={goToSupportHandler}
        size={'default'}
        color={'secondary'}
        data-testid={'button-support'}
      >
        {t('button-support')}
      </Button>
      <CopyButton
        color={'primary'}
        size={'default'}
        data-testid={'button-copy-id'}
        textToCopy={`userId: ${userId}, deviceId: ${deviceId}, version: ${version}`}
        className={s.copyButton}
      >
        {t('button-copy-id', { id: userId })} <Copy className={s.copyIcon} />
      </CopyButton>
      <Button
        onClick={closeHandler}
        size={'default'}
        color={'tertiary'}
        data-testid={'button-close'}
      >
        {t('button-close')}
      </Button>
    </div>
  );
};
