import { FC } from 'react';
import { ConnectionState } from '@app/storage/local/types/persistentTypes';
import { LoadingIcon, PauseIcon, PlayIcon, ErrorIcon } from '../icons';

interface Props {
  status: ConnectionState | 'disabled';
  className: string;
}

export const StatusIcon: FC<Props> = ({ status, className }) => {
  if (status === 'connected') {
    return <PauseIcon className={className} />;
  }
  if (status === 'connecting') {
    return <LoadingIcon className={className} />;
  }
  if (status === 'error') {
    return <ErrorIcon className={className} />;
  }
  return <PlayIcon className={className} />;
};
