import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Error } from '../../shared/errors/Error';
import { extensionsMap } from '../constants';
import { FooterButtons } from './FooterButtons/FooterButtons';

import styles from './ProxyControlledByOtherExtensions.module.css';

type ProxyControlledByOtherExtensionsProps = {
  onClick: () => Promise<void>;
  conflictType: 'proxy' | 'adBlocker';
};

export function ProxyControlledByOtherExtensions({
  onClick,
  conflictType,
}: ProxyControlledByOtherExtensionsProps): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'proxy-controlled-by-other-extensions',
  });
  const [dataMonitoring] = useLocalStorage([
    LocalStorageKey.ExtensionMonitoring,
  ]);
  const [name, setName] = useState<string | null>(null);
  const [fullName, setFullName] = useState<string | null>(null);
  const [icon, setIcon] = useState<string | null>(null);

  const extensionTitle = process.env.EXTENSION_TITLE ?? t('title');

  useEffect(() => {
    if (!dataMonitoring?.[conflictType]?.hasConflictingExtension) {
      return;
    }

    const {
      conflictingExtensionIcon,
      conflictingExtensionId,
      conflictingExtensionName,
    } = dataMonitoring[conflictType];
    const extensionNameFromMap = conflictingExtensionId
      ? extensionsMap[conflictingExtensionId]
      : null;

    setIcon(conflictingExtensionIcon ?? null);
    setName(extensionNameFromMap ?? conflictingExtensionName ?? null);
    setFullName(conflictingExtensionName ?? null);
  }, [dataMonitoring, conflictType]);

  return (
    <div className={styles.container}>
      {name ? (
        <Error
          onClose={onClick}
          icon={
            icon ? (
              <img src={icon} alt="extension-icon" className={styles.icon} />
            ) : undefined
          }
          subtitle={t('subtitle-1', {
            name,
            extensionTitle,
          })}
          text={t('text-1', {
            fullName: fullName ? `${fullName}` : undefined,
            extensionTitle,
          })}
          footer={<FooterButtons onOkClick={onClick} />}
        />
      ) : (
        <Error
          subtitle={t('subtitle-2', {
            extensionTitle,
          })}
          text={t('text-2', { extensionTitle })}
          footer={<FooterButtons onOkClick={onClick} />}
        />
      )}
    </div>
  );
}
