import { FC, ReactNode } from 'react';
import { Close } from '@popup/project/youboost/components/shared/icons';
import s from './PopupWrapper.module.css';

interface Props {
  children: ReactNode;
  footer: ReactNode;
  onClose?: () => void;
}

export const PopupWrapper: FC<Props> = ({ footer, children, onClose }) => {
  return (
    <div className={s.overlay}>
      <div className={s['overlay-content']}>
        {!!onClose && (
          <button type="button" className={s['close-button']} onClick={onClose}>
            <Close className={s.closeIcon} />
          </button>
        )}
        <div className={s.body}>
          <div className={s.main}>{children}</div>
          <div className={s.footer}>{footer}</div>
        </div>
      </div>
    </div>
  );
};
