import { FC } from 'react';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import s from './ActiveDevices.module.css';
import { Puzzle } from '@popup/project/youboost/components/shared/icons/Puzzle/Puzzle';
import { Trans, useTranslation } from 'react-i18next';
import { CopyButton } from '@popup/project/youboost/components/shared/CopyButton/ui/CopyButton';
import { Copy } from '@popup/project/youboost/components/shared/icons/Copy/Copy';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

interface Props {
  userId: string;
}

export const ActiveDevices: FC<Props> = ({ userId }) => {
  const { activeDevices, deviceLimit } = usePremiumData();
  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page',
  });
  const [deviceId] = useLocalStorage(LocalStorageKey.DeviceId);

  const activeCount = activeDevices ?? 0;
  const limitCount = deviceLimit ?? 0;

  return (
    <div className={s.box}>
      <div className={s.header}>
        <div className={s.left}>
          <div className={s.title}>
            {t('premium.active-extensions', { count: activeCount })}
          </div>
          <div className={s.label}>
            {t('premium.available-extensions', { count: limitCount })}
          </div>
        </div>
        <div className={s.counter}>
          <Puzzle className={s.puzzle} />
          <div className={s.count}>
            {activeCount} <span>/ {limitCount}</span>
          </div>
        </div>
      </div>
      <div className={s.footer}>
        <CopyButton size={'default'} color={'tertiary'} textToCopy={userId}>
          <span className={s.baseCopyText}>
            <Trans
              t={t}
              i18nKey={'your-id-button'}
              components={{
                id: (
                  <span className={s.id}>
                    <Copy className={s.copy} /> {userId}
                  </span>
                ),
              }}
            />
          </span>
        </CopyButton>
        {deviceId && (
          <CopyButton size={'default'} color={'tertiary'} textToCopy={deviceId}>
            <span className={s.baseCopyText}>
              <Trans
                t={t}
                i18nKey={'active-device-id-button'}
                components={{
                  id: (
                    <span className={s.id}>
                      <Copy className={s.copy} /> {deviceId.slice(0, 4)}
                    </span>
                  ),
                }}
              />
            </span>
          </CopyButton>
        )}
      </div>
    </div>
  );
};
