import type {
  DropdownIndicatorProps,
  OptionProps,
  SingleValueProps,
  Props as SelectProps,
} from 'react-select';
import Select, { components } from 'react-select';
import s from './Dropdown.module.css';
import {
  ChevronDown,
  Lock,
} from '@popup/project/youboost/components/shared/icons';
import clsx from 'clsx';
import {
  BaseOption,
  TypedSelectProps,
} from '@popup/project/youboost/components/shared/Dropdown/types';

interface Props<
  Option extends BaseOption = BaseOption,
  IsMulti extends boolean = false,
> extends TypedSelectProps<Option, IsMulti> {}

const classNames = {
  container: () => s.dropdown,
  indicatorSeparator: () => s.indicatorSeparator,
  dropdownIndicator: () => s.dropdownIndicator,
  valueContainer: () => s.valueContainer,
  control: () => s.control,
  singleValue: () => s.singleValue,
  menu: () => s.menu,
  menuList: () => s.menuList,
} satisfies SelectProps['classNames'];

export const Dropdown = <
  Option extends BaseOption = BaseOption,
  IsMulti extends boolean = false,
>(
  props: Props<Option, IsMulti>,
) => {
  return (
    <Select
      isSearchable={false}
      classNames={classNames}
      components={{
        DropdownIndicator,
        Option,
        SingleValue,
      }}
      {...props}
    />
  );
};

const DropdownIndicator = <
  Option extends BaseOption = BaseOption,
  IsMulti extends boolean = false,
>(
  props: DropdownIndicatorProps<Option, IsMulti>,
) => {
  const { isDisabled } = props;

  return (
    <components.DropdownIndicator {...props}>
      {isDisabled ? (
        <Lock className={s.lock} />
      ) : (
        <ChevronDown
          className={clsx(
            s.arrow,
            props.selectProps.menuIsOpen && s.reverseArrow,
          )}
        />
      )}
    </components.DropdownIndicator>
  );
};
const Option = <
  Option extends BaseOption = BaseOption,
  IsMulti extends boolean = false,
>(
  props: OptionProps<Option, IsMulti>,
) => {
  const { isSelected, isFocused, isDisabled, children } = props;
  return (
    <components.Option
      {...props}
      className={clsx(
        s.option,
        isSelected && s.option_selected,
        isDisabled && s.option_disabled,
        isFocused && s.option_hover,
      )}
    >
      {children}
      {isDisabled && <Lock className={s.lock} />}
    </components.Option>
  );
};

const SingleValue = <
  Option extends BaseOption = BaseOption,
  IsMulti extends boolean = false,
>(
  props: SingleValueProps<Option, IsMulti>,
) => {
  const { isDisabled, className } = props;

  return (
    <components.SingleValue
      className={clsx(className, { [s.disabled]: isDisabled })}
      {...props}
    />
  );
};
