import { useEffect, useMemo, useState } from 'react';

import {
  defaultPriceRu,
  discountPriceRu,
} from '@popup/project/youboost/components/TariffsContent/constants';
import { useTranslation } from 'react-i18next';
import { getMetaApiData } from '@utils/metaApiData';
import { convertPriceToEuro } from '../components/TariffsContent/utils';

export const useGetConvertedDiscountPrice = () => {
  const { i18n } = useTranslation();
  const [discountPrice, setDiscountPrice] = useState<number | null>(null);
  const [price, setPrice] = useState<number | null>(null);

  useEffect(() => {
    (async function () {
      try {
        const { rubleToEuroRate } = await getMetaApiData();

        setPrice(
          convertPriceToEuro(
            defaultPriceRu,
            i18n.language !== 'ru' ? rubleToEuroRate : 1,
          ),
        );
        setDiscountPrice(
          convertPriceToEuro(
            discountPriceRu,
            i18n.language !== 'ru' ? rubleToEuroRate : 1,
          ),
        );
      } catch (error) {
        console.error(
          'Error in useGetConvertedDiscountPrice:getMetaApiData:',
          error,
        );
      }
    })();
  }, [i18n.language]);

  return useMemo(() => ({
    price,
    discountPrice,
  }), [price, discountPrice]);
};
