import * as Sentry from '@sentry/react';

interface CriticalErrorContext {
  component: string;
  action?: string;
  userId?: string;
  deviceId?: string;
  paymentMethod?: string;
  tariffName?: string;
  apiBaseUrl?: string;
  [key: string]: any;
}

/**
 *    popup  Sentry  
 */
export function logCriticalErrorSentry(
  error: Error,
  context: CriticalErrorContext,
  level: 'error' | 'warning' = 'error',
): void {
  Sentry.withScope((scope) => {
    //  
    scope.setTag('component', context.component);
    if (context.action) {
      scope.setTag('action', context.action);
    }

    //  
    scope.setLevel(level);

    //  
    scope.setContext('critical_error', {
      component: context.component,
      action: context.action,
      userId: context.userId,
      deviceId: context.deviceId,
      paymentMethod: context.paymentMethod,
      tariffName: context.tariffName,
      apiBaseUrl: context.apiBaseUrl,
    });

    //  breadcrumb
    scope.addBreadcrumb({
      message: `Critical error in ${context.component}`,
      category: 'error',
      level: level,
      data: {
        action: context.action,
        errorMessage: error.message,
      },
    });

    //  
    Sentry.captureException(error);
  });
}

/**
 *    
 */
export function logPaymentErrorSentry(
  error: Error,
  paymentMethod: string,
  tariffName: string,
  userId?: string,
  isAutoRenew?: boolean,
): void {
  logCriticalErrorSentry(error, {
    component: 'PaymentFlow',
    action: 'payment_processing',
    paymentMethod,
    tariffName,
    userId,
    isAutoRenew,
  });
}

/**
 *   
 */
export function logAuthErrorSentry(
  error: Error,
  action: string,
  userId?: string,
): void {
  logCriticalErrorSentry(error, {
    component: 'UserAuthentication',
    action,
    userId,
  });
}
