import { clsx } from 'clsx';
import { type CancelPage } from '../../types/CancelPage';
import styles from './Problem.module.css';
import { Trans, useTranslation } from 'react-i18next';
import { Error } from '../../../../../shared/errors/Error';
import { FooterButtons } from './FooterButtons/FooterButtons';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { ToolIcon } from './ToolIcon/ToolIcon';

type ProblemProps = {
  page: CancelPage;
  onNext: () => void;
};

export function Problem({ onNext, page }: ProblemProps): JSX.Element | null {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.problem',
  });

  const navigate = useNavigate();

  return (
    <div
      className={clsx(styles.wrapper, { [styles.out]: page !== 'problem' })}
      data-attribute="problem"
    >
      <Error
        title={t('title')}
        icon={<ToolIcon />}
        subtitle={t('subtitle')}
        text={<Trans t={t} i18nKey="description" />}
        footer={<FooterButtons onNext={onNext} />}
        onClose={() => {
          navigate(RoutePath.Root);
        }}
      />
    </div>
  );
}
