import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import styles from './FooterButtons.module.css';
import { browser_api } from '@app/browser_api';

const SUPPORT_URL = process.env.SUPPORT_PREMIUM_URL;

export const FooterButtons = ({
  onNext,
}: {
  onNext: () => void;
}): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.problem.actions',
  });

  const handleSupport = () => {
    browser_api.tabs.create({
      url: SUPPORT_URL,
      active: true,
    });
  };

  return (
    <div className={styles.buttons}>
      <Button color="primary" size="default" onClick={handleSupport}>
        {t('contact')}
      </Button>
      <Button color="tertiary" size="default" onClick={onNext}>
        {t('continue')}
      </Button>
    </div>
  );
};
