import { FC } from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { IconTextSection } from '@popup/project/youboost/components/IconTextSection';
import { SmileMessage } from '@popup/project/youboost/components/shared/icons';
import { QRCodeSVG } from 'qrcode.react';
import s from './Body.module.css';

interface Props {
  supportUrl: string;
}

export const Body: FC<Props> = ({ supportUrl }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'support-page' });

  return (
    <div className={s.body} data-testid={'settings-body'}>
      <IconTextSection
        icon={<SmileMessage className={s.icon} />}
        text={
          <span className={s.description}>
            <Trans t={t} i18nKey="description" components={{ br: <br /> }} />
          </span>
        }
      >
        <div className={s.qrBox}>
          <QRCodeSVG
            value={supportUrl}
            size={136}
            bgColor={'transparent'}
            fgColor={'var(--qr-code-color)'}
            title={supportUrl}
          />
          <div className={s.url}>{supportUrl.replace('https://', '')}</div>
        </div>
      </IconTextSection>
    </div>
  );
};
