import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { DebugButton } from '@popup/project/youboost/components/shared/DebugButton';
import styles from './Footer.module.css';
import { browser_api } from '@app/browser_api';

const SUPPORT_URL = process.env.SUPPORT_URL;

export const Footer = (): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'error.default.buttons',
  });

  const handleSupport = () => {
    browser_api.tabs.create({
      url: SUPPORT_URL,
      active: true,
    });
  };

  return (
    <div className={styles.footer}>
      <Button color="secondary" size="default" onClick={handleSupport}>
        {t('support')}
      </Button>
      <DebugButton />
    </div>
  );
};
