import { useTranslation } from 'react-i18next';
import { IconTextSection } from '@popup/project/youboost/components/IconTextSection';
import styles from './MainSection.module.css';

type MainSectionProps = {
  icon: React.ReactNode;
  subtitle: string;
  text: React.ReactNode;
  textLink?: React.ReactNode;
  code?: number;
  children?: React.ReactNode;
};

export const MainSection = ({
  icon,
  subtitle,
  text,
  textLink,
  code,
  children,
}: MainSectionProps): JSX.Element => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'error.default',
  });

  return (
    <IconTextSection
      icon={icon}
      title={subtitle}
      text={
        <>
          {text} {textLink && textLink}
        </>
      }
    >
      {children}
      {code && <p className={styles.code}>{`${t('code')}: ${code}`}</p>}
    </IconTextSection>
  );
};
