import { FC } from 'react';
import { clsx } from 'clsx';
import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { ButtonProps } from '@popup/project/youboost/components/shared/Button/types';
import { useCopyId } from '@popup/shared/components/CopyId/hooks/useCopyId';
import copy from 'copy-to-clipboard';
import s from './CopyButton.module.css';

interface Props extends Omit<ButtonProps, 'onClick'> {
  textToCopy: string;
}

export const CopyButton: FC<Props> = ({ textToCopy, children, ...props }) => {
  const { t: sharedT } = useTranslation('shared', {
    keyPrefix: 'debug-button',
  });
  const { handleCopy, isCopied } = useCopyId();

  const copyHandler = () => {
    if (isCopied) return;
    copy(textToCopy, { debug: true });
    handleCopy();
  };

  return (
    <Button onClick={copyHandler} {...props}>
      <span className={clsx(s.baseCopyText, isCopied && s.hide)}>
        {children}
      </span>

      <span className={clsx(s.copiedText, isCopied && s.show)}>
        {' '}
        {sharedT('copied')}
      </span>
    </Button>
  );
};
