import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import styles from './FooterButtons.module.css';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

export const FooterButtons = (): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'free-info-persuasion.actions',
  });

  const navigate = useNavigate();

  return (
    <div className={styles.buttons}>
      <Button
        color="primary"
        size="big"
        onClick={() => navigate(RoutePath.Tariffs)}
      >
        {t('go-premium')}
      </Button>
      <Button
        color="tertiary"
        size="default"
        onClick={() => navigate(RoutePath.PremiumInfoPersuasion)}
      >
        {t('continue')}
      </Button>
    </div>
  );
};
