import { FC } from 'react';
import s from './Footer.module.css';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { browser_api } from '@app/browser_api';
import { Actions } from '@app/actions/shared';
import { ChangeServerManuallyMessage } from '@app/messages/shared';

interface Props {}

export const Footer: FC<Props> = ({}) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'change-server' });
  const navigate = useNavigate();

  const changeHandler = async () => {
    try {
      await browser_api.runtime.sendMessage<ChangeServerManuallyMessage>({
        action: Actions.ChangeServerManually,
      });
      navigate(RoutePath.ServerChanged);
    } catch (e) {
      navigate(RoutePath.Support);
    }
  };

  return (
    <div className={s.buttons}>
      <Button color="secondary" size="default" onClick={changeHandler}>
        {t('change-btn')}
      </Button>
      <Button
        color="tertiary"
        size="default"
        onClick={() => navigate(RoutePath.Support)}
      >
        {t('support-btn')}
      </Button>
    </div>
  );
};
