import type { SuccessResponse, ErrorResponse } from '@api/gifts/types';

export const isSuccessResponse = (value: unknown): value is SuccessResponse => {
  if (typeof value !== 'object' || value === null) return false;
  const obj = value as Partial<SuccessResponse>;

  return 'data' in obj && typeof obj.data === 'object' && obj.data !== null;
};

export const isErrorResponse = (value: unknown): value is ErrorResponse => {
  if (typeof value !== 'object' || value === null) return true;
  const obj = value as Partial<ErrorResponse>;

  return (
    ('error' in obj && !!obj.error && typeof obj.error === 'string') ||
    ('data' in obj && obj.data === null)
  );
};
