import { useNavigate } from 'react-router-dom';

import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { ClockIcon } from '@popup/shared/components/ClockIcon';
import { Trans, useTranslation } from 'react-i18next';
import { SecondaryWrapper } from '../../shared/SecondaryWrapper';

import type { HandleClickTryPremiumType } from '@popup/project/youboost/hooks/useFreePremiumTrial';

import styles from './PremiumPromo.module.css';

export type PremiumPromoProps = {
  handleClickTryPremium: HandleClickTryPremiumType;
};

export function PremiumPromo({
  handleClickTryPremium,
}: PremiumPromoProps): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'premium-info.promo',
  });
  const navigate = useNavigate();

  return (
    <>
      <SecondaryWrapper
        header={t('title')}
        closeClickHandler={() => {
          navigate(RoutePath.Root);
        }}
        footer={
          <div className={styles.buttons}>
            <Button
              color="primary"
              size="default"
              onClick={async () => {
                await handleClickTryPremium({ isAutoRenew: false });
              }}
            >
              {t('actions.try-for-free')}
            </Button>
          </div>
        }
      >
        <h3 className={styles.title}>{t('subtitle')}</h3>
        <div className={styles['clock-wrapper']}>
          <ClockIcon />
          <div className={styles['clock-text']}>
            <Trans
              t={t}
              i18nKey="hours"
              count={24}
              components={{ tag: <span /> }}
            />
          </div>
        </div>
        <p className={styles.text}>{t('description')}</p>
      </SecondaryWrapper>
    </>
  );
}
