import { httpClient } from '@api/httpClient';
import { getCancelAutoRenewApiUrl } from '@api/urls';
import { getMetaApiData } from '@utils/metaApiData';
import { HTTPError } from 'ky';
import {
  CancelAutoRenewDtoType,
  CancelAutoRenewResponseType,
  CancelAutoRenewType,
} from '@api/premium/cancelAutoRenew/types';

export const cancelAutoRenew: CancelAutoRenewType = async (body) => {
  try {
    const { apiBaseUrl } = await getMetaApiData();
    const cancelSubscriptionApiUrl = getCancelAutoRenewApiUrl(apiBaseUrl);
    return await httpClient
      .post<CancelAutoRenewResponseType>(cancelSubscriptionApiUrl, {
        json: body satisfies CancelAutoRenewDtoType,
      })
      .json();
  } catch (error) {
    if (error instanceof HTTPError) {
      if (error.response.status === 400) {
        const errorData = await error.response.json<{
          errors: { code: string; description: string }[];
        }>();

        throw new Error(errorData.errors[0]?.description);
      }
      if (error.response.status === 500) {
        throw new Error('error-messages.api-http-error-500');
      }
    }
    if (error instanceof Error && error.message === 'Failed to fetch') {
      throw new Error('error-messages.api-http-error-500');
    }
    throw new Error('error-messages.api-http-error-500');
  }
};
