import { forwardRef } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { Error } from '../../../../../shared/errors/Error';
import { Button } from '../../../../../shared/Button';
import { FileLockIcon } from '@popup/project/youboost/components/shared/icons';
import { browser_api } from '@app/browser_api';

import styles from './ForgetIdStep.module.css';

const TELEGRAM_GET_ID_LINK = process.env.TELEGRAM_GET_ID_LINK;
const TELEGRAM_HELP_BOT_LINK = process.env.TELEGRAM_HELP_BOT_LINK;

type ForgetIdStepProps = {
  onStepChange: (step: number) => void;
};

export const ForgetIdStep = forwardRef<HTMLDivElement, ForgetIdStepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost', {
      keyPrefix: 'onboarding.forget-id-step',
    });

    function handleClickBack() {
      onStepChange(1);
    }

    function handleClickHelp() {
      browser_api.tabs.create({ url: TELEGRAM_HELP_BOT_LINK });
    }
    return (
      <Error
        onClose={() => {
          onStepChange(0);
        }}
        title={t('title')}
        subtitle={t('subtitle')}
        text={<Trans t={t} i18nKey="description" />}
        textLink={
          <a
            className={styles.link}
            href={TELEGRAM_GET_ID_LINK}
            target="_blank"
          >
            {TELEGRAM_GET_ID_LINK}
          </a>
        }
        icon={<FileLockIcon className={styles.icon} />}
        footer={
          <div className={styles.buttons}>
            <Button onClick={handleClickHelp} size={'big'} color={'secondary'}>
              {t('actions.help')}
            </Button>
            <Button onClick={handleClickBack} size={'big'} color={'tertiary'}>
              {t('actions.go-back')}
            </Button>
          </div>
        }
      ></Error>
    );
  },
);

ForgetIdStep.displayName = 'ForgetIdStep';
