import { useEffect, useState } from 'react';
import { getMetaApiData } from '@utils/metaApiData';
import {
  PaymentMethod,
  PaymentMethodV2Type,
} from '@app/features/types/paymentMethodTypes';
import { useTranslation } from 'react-i18next';

export function usePaymentMethods() {
  const { i18n } = useTranslation();
  const [paymentMethods, setPaymentMethods] = useState<PaymentMethodV2Type[]>(
    [],
  );

  useEffect(() => {
    const getMetaData = async () => {
      const data = await getMetaApiData();
      let methods;
      if (i18n.language === 'ru') {
        methods = data.availablePaymentMethodsV3?.filter(
          (el) => el.type !== PaymentMethod.CardInt,
        );
      } else {
        methods = data.availablePaymentMethodsV3?.filter(
          (el) =>
            el.type !== PaymentMethod.Sbp && el.type !== PaymentMethod.Card,
        );
      }
      if (!methods) return;
      setPaymentMethods(methods);
    };
    void getMetaData();
  }, [i18n.language]);

  return paymentMethods;
}
