import { FC } from 'react';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper/ui/SecondaryWrapper';
import { useTranslation } from 'react-i18next';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import s from './subscriptionManagement.module.css';
import {
  ActiveDevices,
  FreeData,
  FreeDataFooter,
  PremiumData,
} from '@popup/project/youboost/components/SubscriptionManagementContent';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

interface Props {}

export const SubscriptionManagement: FC<Props> = ({}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page',
  });
  const navigate = useNavigate();

  const { hasPremium, userId } = usePremiumData();

  if (!userId) return null;

  const closeClickHandler = () => {
    navigate(RoutePath.Root);
  };

  return (
    <SecondaryWrapper
      header={t('title')}
      footer={hasPremium ? undefined : <FreeDataFooter userId={userId} />}
      closeClickHandler={closeClickHandler}
    >
      <div className={s.subscriptionManagement}>
        {hasPremium ? (
          <>
            <PremiumData />
            <ActiveDevices userId={userId} />
          </>
        ) : (
          <FreeData />
        )}
      </div>
    </SecondaryWrapper>
  );
};
