import { fetchMetaApiData } from '@api/meta';
import { MetaApiData } from '@app/storage/local/types/expirableTypes';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

const DEFAULT_TTL_META_API_DATA_CACHE_MS = 60 * 60 * 1000; // 1 hour

const TTL_META_API_DATA_CACHE_MS = (() => {
  const rawTtl = process.env.TTL_META_API_DATA_CACHE_MS;

  if (!rawTtl) {
    return DEFAULT_TTL_META_API_DATA_CACHE_MS;
  }

  const parsed = Number.parseInt(rawTtl, 10);

  return Number.isFinite(parsed) && parsed > 0
    ? parsed
    : DEFAULT_TTL_META_API_DATA_CACHE_MS;
})();

export async function getMetaApiData(): Promise<MetaApiData> {
  const { metaApiDataLastUpdatedMs, metaApiData } = await getLocalStorageData([
    LocalStorageKey.MetaApiDataLastUpdatedMs,
    LocalStorageKey.MetaApiData,
  ]);

  const timestamp =
    typeof metaApiDataLastUpdatedMs === 'number' &&
    Number.isFinite(metaApiDataLastUpdatedMs)
      ? metaApiDataLastUpdatedMs
      : undefined;

  const nowMs = Date.now();

  if (
    metaApiData &&
    timestamp !== undefined &&
    nowMs - timestamp <= TTL_META_API_DATA_CACHE_MS
  ) {
    return metaApiData;
  }

  const data = await fetchMetaApiData();
  const updatedAtMs = Date.now();

  await setLocalStorageData({
    [LocalStorageKey.MetaApiData]: data,
    [LocalStorageKey.MetaApiDataLastUpdatedMs]: updatedAtMs,
  });

  return data;
}
