import { useEffect, useState } from 'react';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { getNegativeFeedbackUrl } from '@popup/project/youboost/components/shared/popups/Feedback/utils';
import { NegativeFeedbackUrlState } from '@popup/project/youboost/components/shared/popups/Feedback/types';

const baseUrl = process.env.POPUP_HTML_FEEDBACK_BOT_URL;

export function useNegativeFeedbackUrl(): { negativeFeedbackUrl: string } {
  const [state, setState] = useState<NegativeFeedbackUrlState>({
    proxy: undefined,
    userId: undefined,
    version: undefined,
  });

  useEffect(() => {
    async function getStatus(): Promise<void> {
      const { lastProxyHost, userId } = await getLocalStorageData([
        LocalStorageKey.LastProxyHost,
        LocalStorageKey.UserId,
      ]);

      const version = browser_api.runtime.getManifest().version;

      setState((prev) => ({
        ...prev,
        proxy: lastProxyHost,
        userId,
        version,
      }));
    }

    void getStatus();
  }, []);

  useEffect(() => {
    function listener(changes: { lastProxyHost?: { newValue: string } }): void {
      if (changes.lastProxyHost) {
        setState((prev) => ({
          ...prev,
          proxy: changes.lastProxyHost?.newValue,
        }));
      }
    }

    browser_api.storage.local.onChanged.addListener(listener);
    return () => {
      browser_api.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  return {
    negativeFeedbackUrl: getNegativeFeedbackUrl({ ...state, baseUrl }),
  };
}
