import type { NegativeFeedbackUrlState } from '@popup/project/youboost/components/shared/popups/Feedback/types';

export const getNegativeFeedbackUrl = ({
  baseUrl,
  version,
  proxy,
  userId,
}: NegativeFeedbackUrlState & { baseUrl: string }): string => {
  const body = [proxy, userId, version];

  return `${baseUrl}?start=${btoa(JSON.stringify(body))}`;
};
