import { logger } from './logger';

type ClickLoggerOptions = {
  logLinks?: boolean;
  logButtons?: boolean;
  logOtherElements?: boolean;
};

export class ClickLogger {
  private static instance: ClickLogger;
  private isEnabled = false;
  private clickHandler: ((event: MouseEvent) => void) | null = null;
  private options!: Required<ClickLoggerOptions>;

  constructor(options: ClickLoggerOptions = {}) {
    if (ClickLogger.instance) {
      return ClickLogger.instance;
    }

    this.options = {
      logLinks: options.logLinks ?? true,
      logButtons: options.logButtons ?? true,
      logOtherElements: options.logOtherElements ?? false,
    };

    ClickLogger.instance = this;
  }

  private getElementInfo(element: HTMLElement): string {
    const tagName = element.tagName.toLowerCase();
    const id = element.id || 'no id';
    const text = element.textContent?.trim().slice(0, 50) || '';
    const href = element.getAttribute('href') || '';

    const parts: string[] = [];

    if (tagName === 'a') {
      parts.push('[Link]');
      if (href) parts.push(`href: ${href}`);
    } else if (tagName === 'button') {
      parts.push('[Button]');
    } else {
      parts.push(`[${tagName}]`);
    }

    parts.push(`id: ${id}`);

    if (text) parts.push(`text: "${text}"`);

    return parts.join(', ');
  }

  private findClickableElement(
    element: HTMLElement | null,
  ): HTMLElement | null {
    if (!element) return null;

    const { logLinks, logButtons } = this.options;
    let current: HTMLElement | null = element;

    while (current) {
      if (!current.tagName) {
        current = current.parentElement;
        continue;
      }

      const tagName = current.tagName.toLowerCase();

      if (tagName === 'a' && logLinks) {
        return current;
      }

      if (tagName === 'button' && logButtons) {
        return current;
      }

      current = current.parentElement;
    }

    return null;
  }

  private handleClick = (event: MouseEvent): void => {
    if (!event?.target) return;

    const target = event.target as HTMLElement;
    if (!target) return;

    const clickableElement = this.findClickableElement(target);
    if (!clickableElement) return;

    const elementInfo = this.getElementInfo(clickableElement);
    logger.info(`[Click Logger] Click: ${elementInfo}`);
  };

  public init(): void {
    if (this.isEnabled) {
      logger.warn('[Click Logger] Logger already initialized');
      return;
    }

    if (typeof document === 'undefined' || !document.addEventListener) {
      logger.error('[Click Logger] Document is not available');
      return;
    }

    this.clickHandler = this.handleClick;

    document.removeEventListener('click', this.clickHandler, true);

    document.addEventListener('click', this.clickHandler, true);

    this.isEnabled = true;
    logger.debug('[Click Logger] Click logger initialized');
  }

  public destroy(): void {
    if (!this.isEnabled || !this.clickHandler) return;

    if (typeof document !== 'undefined' && document.removeEventListener) {
      document.removeEventListener('click', this.clickHandler, true);
    }

    this.clickHandler = null;
    this.isEnabled = false;
  }

  public getIsEnabled(): boolean {
    return this.isEnabled;
  }
}

export type { ClickLoggerOptions };
