import styles from './ConnectButton.module.css';
import { ConnectionState } from '@app/storage/local/types/persistentTypes';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import { StatusIcon } from '@popup/project/youboost/components/ConnectButton/ui/StatusIcon/StatusIcon';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { useMemo } from 'react';

type ConnectButtonProps = {
  status: ConnectionState;
  disabled?: boolean;
  className?: string;
  onClick: () => void;
};

export const ConnectButton = ({
  status: connectionStatus,
  disabled = false,
  className,
  onClick,
}: ConnectButtonProps) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'root.connect-proxy' });

  const [isConnectionButtonDisabled] = useLocalStorage(
    LocalStorageKey.IsConnectionButtonDisabled,
  );

  const status = useMemo(() => {
    if (isConnectionButtonDisabled) {
      return 'error';
    }

    if (connectionStatus === 'disconnected' && disabled) {
      return 'disabled';
    }

    return connectionStatus;
  }, [connectionStatus, disabled, isConnectionButtonDisabled]);

  const isDisabled = useMemo(
    () => isConnectionButtonDisabled || status === 'connecting',
    [isConnectionButtonDisabled, status],
  );

  return (
    <button
      className={clsx(styles['connect-button'], styles[status], className, {
        [styles.disabled]: disabled || isConnectionButtonDisabled,
      })}
      disabled={isDisabled}
      onClick={onClick}
    >
      <StatusIcon status={status} className={styles.icon} />
      <span>
        {t(status)}
        {status === 'error' ? (
          <>
            <br></br>
            <span className={styles['error-description']}>
              {t('try-in-5-min')}
            </span>
          </>
        ) : null}
      </span>
    </button>
  );
};
