import { FC } from 'react';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useTranslation } from 'react-i18next';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { acceptGift } from '@api/gifts';
import { getAcceptGiftApiUrl } from '@api/urls';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useNavigate } from 'react-router-dom';
import { browser_api } from '@app/browser_api';
import s from './Footer.module.css';
import { Actions } from '@app/actions/shared';
import { RefuseGiftMessage } from '@app/messages/shared';

interface Props {
  imageUrl: string;
}

export const Footer: FC<Props> = ({ imageUrl }) => {
  const navigate = useNavigate();
  const { t } = useTranslation('youboost', { keyPrefix: 'refuse-gift' });

  const refuseHandler = async () => {
    await browser_api.runtime.sendMessage<RefuseGiftMessage>({
      action: Actions.RefuseGift,
    });
    navigate(RoutePath.Root);
  };

  const getGiftHandler = async () => {
    try {
      const { userId, deviceId, metaApiData } = await getLocalStorageData([
        LocalStorageKey.UserId,
        LocalStorageKey.DeviceId,
        LocalStorageKey.MetaApiData,
      ]);

      if (!metaApiData) {
        console.error('MetaApiData is not defined.');
        return;
      }

      const { apiBaseUrl } = metaApiData;

      if (!userId) {
        console.error('User ID is not defined.');
        return;
      }
      if (!deviceId) {
        console.error('Device ID is not defined.');
        return;
      }

      const { data } = await acceptGift(getAcceptGiftApiUrl(apiBaseUrl), {
        userId,
        deviceId,
      });

      await setLocalStorageData(data.subscription);
      navigate(RoutePath.GiftAdvertising, { state: { imageUrl } });
    } catch (e) {
      console.error('Error accepting gift:', e);
    }
  };
  return (
    <div className={s.footer}>
      <Button size={'default'} color={'primary'} onClick={getGiftHandler}>
        {t('get-btn')}
      </Button>
      <Button size={'default'} color={'secondary'} onClick={refuseHandler}>
        {t('refuse-btn')}
      </Button>
    </div>
  );
};
