import { FC, MutableRefObject, useEffect, useState } from 'react';
import { SelectDeviceLimit } from '@popup/project/youboost/components/TariffsContent/ui/SelectDeviceLimit/SelectDeviceLimit';
import { SelectTariff } from '@popup/project/youboost/components/TariffsContent/ui/SelectTariff/SelectTariff';
import { BlackFriday } from '@popup/project/youboost/components/TariffsContent/ui/BlackFriday/BlackFriday';
import { FetchTariffsResponse, TariffData } from '@api/urls';
import { LimitType } from '@popup/project/youboost/components/TariffsContent/types';
import { getDeviceLimits } from '@popup/project/youboost/components/TariffsContent/utils';
import { useTariffSelection } from '@popup/project/youboost/components/TariffsContent/hooks/useTariffSelection';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { Feature } from '@app/features/types/Feature';
import styles from './Body.module.css';

interface Props {
  tariffs: FetchTariffsResponse;
  selectedTariffRef: MutableRefObject<TariffData | null>;
  hasOneMonthDiscount: boolean;
  discountPrice?: number;
}

export const Body: FC<Props> = ({
  tariffs,
  selectedTariffRef,
  hasOneMonthDiscount,
  discountPrice,
}) => {
  const deviceLimits = getDeviceLimits(tariffs);
  const [selectedLimit, setSelectedLimit] = useState<LimitType>(
    deviceLimits[0],
  );

  const { tariffsByLimit, selectedTariff, handleTariffSelect } =
    useTariffSelection({
      tariffs,
      selectedLimit,
      hasOneMonthDiscount,
    });
  const { featuresList } = useFeaturesList();

  useEffect(() => {
    selectedTariffRef.current = selectedTariff;
  }, [selectedTariff, selectedTariffRef]);

  return (
    <>
      <div className={styles.container}>
        {featuresList[Feature.BlackFridayOn] && <BlackFriday />}
        <SelectDeviceLimit
          deviceLimits={deviceLimits}
          selectedLimit={selectedLimit}
          setSelectedLimit={setSelectedLimit}
        />
        <SelectTariff
          tariffsByLimit={tariffsByLimit}
          selectedTariff={selectedTariff}
          handleTariffSelect={handleTariffSelect}
          hasOneMonthDiscount={hasOneMonthDiscount}
          discountPrice={discountPrice}
        />
      </div>
    </>
  );
};
