import { browser_api } from '@app/browser_api';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { SettingsButton } from './SettingsButton/SettingsButton';
import { Logo } from '@popup/project/youboost/components/shared/Header/ui/Logo/Logo';
import styles from './Header.module.css';

interface Props {
  showLogo?: boolean;
}

export function Header({ showLogo }: Props): JSX.Element {
  const [isDevMode] = useLocalStorage(LocalStorageKey.IsDevMode, false);
  const [deviceId] = useLocalStorage(LocalStorageKey.DeviceId);
  const version = browser_api.runtime.getManifest().version;
  const trimmedDeviceId = deviceId?.slice(0, 4);

  return (
    <>
      <div className={styles.header} data-testid="header">
        {isDevMode && (
          <label className={styles.devModeLabel}>
            <span> DEV MODE </span>
          </label>
        )}
        <div className={styles.left}>
          {showLogo && <Logo className={styles.logo} />}
          <div className={styles['info-container']}>
            <span className={styles.version}>ver. {version}</span>
            {trimmedDeviceId && (
              <span className={styles.version}>d: {trimmedDeviceId}</span>
            )}
          </div>
        </div>
        <SettingsButton />
      </div>
    </>
  );
}
