import { TelegramIcon } from './TelegramIcon';
import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { browser_api } from '@app/browser_api';

import styles from './TelegramPromo.module.css';

type TelegramPromoProps = {
  className?: string;
  startParam?: string | null;
};

export function TelegramPromo({
  className,
  startParam,
}: TelegramPromoProps): JSX.Element {
  const { t } = useTranslation('shared', {
    keyPrefix: 'telegram-promo',
  });

  const handleClickTelegram = async () => {
    await browser_api.tabs.create({
      url: `https://t.me/${process.env.TELEGRAM_URL}${startParam ? `?start=${startParam}` : ''}`,
    });
  };

  return (
    <Button
      onClick={handleClickTelegram}
      size="small"
      color="secondary"
      className={className}
      data-testid="telegram-promo"
    >
      <TelegramIcon className={styles.telegram} />
      <span>{t('channel')}</span>
    </Button>
  );
}
