import { FC, useMemo } from 'react';
import {
  Tabs,
  type TabItem,
} from '@popup/project/youboost/components/shared/Tabs';
import { useTranslation } from 'react-i18next';
import { TariffData } from '@api/urls';
import type { Tariff } from '@api/premium/types/Tariff';
import { TariffItem } from '@popup/project/youboost/components/TariffsContent/ui/TariffItem/TariffItem';

import s from './SelectTariff.module.css';

interface Props {
  tariffsByLimit: TariffData[];
  selectedTariff: TariffData;
  handleTariffSelect: (tariff: Tariff) => void;
  hasOneMonthDiscount: boolean;
  discountPrice?: number;
}

const getTariffsItems = ({
  hasOneMonthDiscount,
  tariffsByLimit,
  discountPrice,
}: {
  hasOneMonthDiscount: boolean;
  tariffsByLimit: TariffData[];
  discountPrice?: number;
}): TabItem<Tariff>[] => {
  return tariffsByLimit.map((tariffData, index) => {
    const id = tariffData.tariff;
    return {
      id,
      label: (
        <TariffItem
          {...tariffData}
          hasOneMonthDiscount={hasOneMonthDiscount}
          isAccent={index === tariffsByLimit.length - 1}
          discountPrice={discountPrice}
        />
      ),
      content: null,
      dataTestId: id,
    };
  });
};

export const SelectTariff: FC<Props> = ({
  tariffsByLimit,
  selectedTariff,
  handleTariffSelect,
  hasOneMonthDiscount,
  discountPrice,
}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'tariffs-page',
  });

  const tariffItems = useMemo(
    () =>
      getTariffsItems({
        hasOneMonthDiscount,
        tariffsByLimit,
        discountPrice,
      }),
    [tariffsByLimit, hasOneMonthDiscount, discountPrice],
  );

  return (
    <div className={s.box}>
      <div className={s.box}>
        <div className={s.header}>
          <div className={s.title}>{t('tariff-title')}</div>
          <div className={s.label}>{t('tariff-label')}</div>
        </div>
        <Tabs<Tariff>
          items={tariffItems}
          orientation={'vertical'}
          onChangeActiveTab={handleTariffSelect}
          activeTabId={selectedTariff.tariff}
        />
      </div>
    </div>
  );
};
