import { FC } from 'react';
import { PaymentMethod } from '@app/features/types/paymentMethodTypes';
import { Sbp } from '@popup/project/youboost/components/PaymentContent/ui/icons/Sbp/Sbp';
import s from './MethodItem.module.css';
import { Cards } from '@popup/project/youboost/components/PaymentContent/ui/Cards/Cards';
import { useTranslation } from 'react-i18next';

interface Props {
  method: PaymentMethod;
}

export const MethodItem: FC<Props> = ({ method }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'payment-page' });
  return (
    <div className={s.item}>
      {method === 'sbp' && (
        <>
          {t('spb')} <Sbp />
        </>
      )}
      {method === 'card' && (
        <>
          {t('card')} <Cards />
        </>
      )}
      {method === 'card_int' && (
        <>
          {t('card')} <Cards />
        </>
      )}

      {method === 'crypto' && <>{t('crypto')}</>}
    </div>
  );
};
