import { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import { SettingsList } from '@popup/project/youboost/components/SettingsContent';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '../../types/RoutePath';
import { FooterButtons } from './FooterButtons/FooterButtons';

interface Props {}

export const Settings: FC<Props> = ({}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'settings-page',
  });
  const navigate = useNavigate();
  return (
    <SecondaryWrapper
      header={t('title')}
      footer={<FooterButtons />}
      backButtonProps={{
        onClick: () => navigate(-1),
      }}
      closeClickHandler={() => navigate(RoutePath.Root)}
    >
      <SettingsList />
    </SecondaryWrapper>
  );
};
