import { FC } from 'react';
import s from './Background.module.css';
import clsx from 'clsx';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

interface Props {}

export const Background: FC<Props> = ({}) => {
  const [status] = useLocalStorage(LocalStorageKey.CurrentState);

  return (
    <div
      className={clsx(s.wrapper, status === 'connected' && s.connected)}
      data-testid="background-wrapper"
    >
      <div className={s['dots-1']} />
      <div className={s['dots-2']} />
      <div
        className={clsx(
          s.stripes,
          status === 'connecting' && s.connecting,
          status === 'connected' && s.connected,
        )}
      />
    </div>
  );
};
