import { useTranslation } from 'react-i18next';
import { useLocation, useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import icon from '@popup/project/youboost/assets/icon.png';
import s from './RefuseGift.module.css';
import { Footer } from '@popup/project/youboost/components/RefuseGiftContent';

type StateType = { imageUrl: string } | undefined;

export const RefuseGift = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation('youboost', { keyPrefix: 'refuse-gift' });

  const state = location.state as StateType;
  const imageUrl = state?.imageUrl ?? icon;

  return (
    <SecondaryWrapper
      header={t('title')}
      footer={<Footer imageUrl={imageUrl} />}
      backButtonProps={{ onClick: () => navigate(-1) }}
      closeClickHandler={() => navigate(RoutePath.Root)}
    >
      <div className={s.header}>
        <div className={s.iconContainer}>
          <img className={s.iconImage} src={imageUrl} alt="icon" />
        </div>
        <div className={s.title}>{t('text')}</div>
      </div>
      <div className={s.description}>{t('description')}</div>
    </SecondaryWrapper>
  );
};
