import { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { FeedbackType } from '@popup/project/youboost/components/shared/popups/Feedback/types';

interface Props {
  actionClickHandler: (state: FeedbackType) => void;
}

export const DefaultFooter: FC<Props> = ({ actionClickHandler }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'root.feedback' });

  return (
    <>
      <Button
        size={'default'}
        color={'primary'}
        onClick={() => actionClickHandler('positive')}
      >
        {t('default.action-yes')}
      </Button>
      <Button
        size={'default'}
        color={'secondary'}
        onClick={() => actionClickHandler('negative')}
      >
        {t('default.action-no')}
      </Button>
    </>
  );
};
