import { FC, useState } from 'react';
import s from './CancelRenewalRow.module.css';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useTranslation } from 'react-i18next';
import { cancelAutoRenew } from '@api/premium/cancelAutoRenew';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

interface Props {
  userId: string;
}

export const CancelRenewalRow: FC<Props> = ({ userId }) => {
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page.premium'
  });
  const { t: sharedT } = useTranslation('shared');


  const cancelAutoRenewalHandler = async () => {
    try {
      const res = await cancelAutoRenew({ userId });
      const data = res.data;
      if (data) {
        await setLocalStorageData({
          [LocalStorageKey.IsAutoRenew]: data.subscription.isAutoRenew
        });
      }
    } catch (error) {
      setErrorMessage(error instanceof Error ? error.message : null);
      console.error(error);
    }
  };

  return (
    <>
      {errorMessage ? (
        <div className={s.error}>{sharedT(errorMessage)}</div>
      ) : (
        <div className={s.row}>
          <div className={s.renewal}>{t('auto-renewal-title')}</div>
          <Button
            size={'small'}
            color={'tertiary'}
            onClick={cancelAutoRenewalHandler}
          >
            {t('cancel-btn')}
          </Button>
        </div>
      )}
    </>
  );
};
