import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';
import { Button } from '../../../shared/Button';

import styles from './FooterButtons.module.css';

const DIAGNOSTIC_URL = process.env.DIAGNOSTIC_EXTENSIONS_URL;

export function FooterButtons({
  onOkClick,
}: {
  onOkClick: () => Promise<void>;
}): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'proxy-controlled-by-other-extensions',
  });
  const handleLinkClick = async () => {
    await onOkClick();
    await browser_api.tabs.create({ url: DIAGNOSTIC_URL });
  };

  return (
    <div className={styles.buttons}>
      <Button
        color="secondary"
        size="default"
        onClick={handleLinkClick}
        data-testid="help-button"
      >
        {t('help-button')}
      </Button>

      <Button
        onClick={onOkClick}
        id="proxy-controlled-btn"
        color="secondary"
        size="default"
        data-testid="ok-button"
      >
        {t('ok-button')}
      </Button>
    </div>
  );
}
