import { useTranslation } from 'react-i18next';
import { useFreePremiumTrial } from '@popup/project/youboost/hooks/useFreePremiumTrial';
import { Error } from '@popup/project/youboost/components/shared/errors/Error';
import { PremiumPromo } from '../../PremiumPromo';
import { LoadingScreen } from '../../shared/LoadingScreen/LoadingScreen';

export function PremiumInfoContent() {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'premium-info',
  });

  const {
    isCheckingFreePremiumTrial,
    canTryFreePremiumTrial,
    isFreePremiumTrialError,
    isFreePremiumTrialLoading,
    handleClickTryPremium,
  } = useFreePremiumTrial();

  if (isFreePremiumTrialError) {
    return <Error />;
  }

  if (isFreePremiumTrialLoading) {
    return (
      <LoadingScreen title={t('loader-trial-title')} text={t('loader-text')} />
    );
  }

  if (isCheckingFreePremiumTrial) {
    return (
      <LoadingScreen title={t('loader-check-title')} text={t('loader-text')} />
    );
  }

  if (!canTryFreePremiumTrial) return null;

  return (
    <>
      <PremiumPromo handleClickTryPremium={handleClickTryPremium} />
    </>
  );
}
