import { FC } from 'react';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import { useTranslation } from 'react-i18next';
import {
  Body,
  Footer,
} from '@popup/project/youboost/components/SupportContent';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

interface Props {}

export const Support: FC<Props> = ({}) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'support-page' });
  const navigate = useNavigate();
  const SUPPORT_URL = process.env.SUPPORT_URL;

  const closeClickHandler = () => {
    navigate(RoutePath.Root);
  };

  if (!SUPPORT_URL) return null;

  return (
    <SecondaryWrapper
      header={t('header')}
      footer={<Footer supportUrl={SUPPORT_URL} />}
      closeClickHandler={closeClickHandler}
    >
      <Body supportUrl={SUPPORT_URL} />
    </SecondaryWrapper>
  );
};
