import { clsx } from 'clsx';
import { useMemo } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

import { Button } from '@popup/project/youboost/components/shared/Button';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

import { RoutePath } from '../../../routes/types/RoutePath';
import { StatusTag } from '../../shared/StatusTag';
import { CalendarHeartIcon } from '../../shared/icons';

import styles from './PremiumData.module.css';
import dayjs from 'dayjs';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export function PremiumData(): JSX.Element | null {
  const navigate = useNavigate();
  const { t } = useTranslation('youboost', {
    keyPrefix: 'root.premium-data',
  });

  const [isAutoRenew] = useLocalStorage(LocalStorageKey.IsAutoRenew);
  const { premiumDaysLeft, userId, secondsLeft, paidUntil } = usePremiumData();
  const paymentStatus = usePaymentStatus();

  const isAvailableStatus = useMemo(
    () => ['Expired', 'New'].includes(paymentStatus ?? ''),
    [paymentStatus],
  );

  function renderTimeLeft(): JSX.Element | null {
    if (premiumDaysLeft === undefined) {
      return null;
    }

    if (premiumDaysLeft > 1) {
      return (
        <div
          className={clsx({
            [styles['no-time-left']]: premiumDaysLeft <= 1,
          })}
        >
          <Trans
            t={t}
            i18nKey="days-remaining"
            components={{ time: <span className={styles.time} /> }}
            count={premiumDaysLeft}
          />
        </div>
      );
    }

    if (!secondsLeft) {
      return (
        <div
          className={clsx({
            [styles['no-time-left']]: premiumDaysLeft <= 1,
          })}
        >
          <Trans t={t} i18nKey="no-time-left" />
        </div>
      );
    }

    const hours = Math.ceil(secondsLeft / 3600);
    return (
      <div
        className={clsx({
          [styles['no-time-left']]: hours <= 3,
        })}
      >
        <Trans
          t={t}
          i18nKey="hours-remaining"
          components={{ time: <span className={styles.time} /> }}
          count={hours}
        />
      </div>
    );
  }

  function handleClickExtendPremium() {
    navigate(RoutePath.Tariffs);
  }

  function handleClickGoFree() {
    navigate(RoutePath.FreeInfoPersuasion);
  }

  function handleClickManageSubscription() {
    navigate(RoutePath.SubscriptionManagement);
  }

  if (userId === undefined || premiumDaysLeft === undefined) {
    return null;
  }

  return (
    <>
      <div className={styles.wrapper} data-testid="premium-data">
        <div className={styles['personal-data']}>
          <div className={styles['payment-container']}>
            <StatusTag status="Premium" />
          </div>
          <div className={styles['payment-details']}>
            <div id="days-left">{renderTimeLeft()}</div>
            {isAutoRenew && paidUntil && (
              <div className={styles['auto-renewal']}>
                {t('auto-renewal', {
                  date: dayjs(paidUntil).format('DD.MM.YY'),
                })}
              </div>
            )}
          </div>
        </div>
        <div className={styles.buttons}>
          <Button
            color="primary"
            size="default"
            className={styles.extend}
            onClick={handleClickExtendPremium}
          >
            {t('extend-premium')}
          </Button>

          {isAvailableStatus ? (
            <Button
              color="secondary"
              size="default"
              onClick={handleClickGoFree}
            >
              {t('go-to-free')}
            </Button>
          ) : (
            <Button
              color="tertiary"
              size="default"
              className={styles.extend}
              onClick={handleClickManageSubscription}
            >
              {t('manage-subscription')}
              <CalendarHeartIcon className={styles.icon} />
            </Button>
          )}
        </div>
      </div>
    </>
  );
}
