import { FC } from 'react';
import s from './RenewalRow.module.css';
import { Switch } from '@popup/project/youboost/components/shared/Switch';
import { useTranslation } from 'react-i18next';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { Feature } from '@app/features/types/Feature';

interface Props {
  clickHandler: () => void;
  enabled: boolean;
}

export const RenewalRow: FC<Props> = ({ clickHandler, enabled }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'payment-page' });
  const { featuresList } = useFeaturesList();
  const isFeatureEnabled = featuresList[Feature.AutoRenewal];

  if (!isFeatureEnabled) return <div className={s.empty} />;

  return (
    <div className={s.row}>
      <div className={s.renewal}>{t('enable-auto-renewal-title')}</div>
      <Switch setOn={clickHandler} on={enabled} />
    </div>
  );
};
