import { httpClient } from '../httpClient';

import type { PaymentMethod } from '@app/features/types/paymentMethodTypes';
import type { Tariff } from '@api/premium/types/Tariff';
import { LocalStorageData } from '@app/storage/local/types/sharedTypes';

export type CreateNewPaymentLinkDto = {
  url: string;
  tariffName: Tariff;
  paymentMethod: PaymentMethod;
  userId: LocalStorageData['userId'];
  isAutoRenew: boolean;
};

type CreateNewPaymentLinkResponse = {
  paymentLink: string;
};

export async function createNewPaymentLink({
  url,
  userId,
  tariffName,
  paymentMethod,
  isAutoRenew,
}: CreateNewPaymentLinkDto): Promise<CreateNewPaymentLinkResponse> {
  const data = await httpClient
    .post<CreateNewPaymentLinkResponse>(url, {
      json: {
        userId,
        tariff: tariffName,
        paymentMethod,
        isAutoRenew,
      },
    })
    .json();
  return data;
}
