import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import styles from './FooterButtons.module.css';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

type FooterButtonsProps = {
  loading?: boolean;
  disabled?: boolean;
  onContinue: () => void;
};

export const FooterButtons = ({
  loading,
  disabled,
  onContinue,
}: FooterButtonsProps): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'premium-sale-persuasion.actions',
  });

  const navigate = useNavigate();

  return (
    <div className={styles.buttons}>
      <Button
        color="tertiary"
        size="default"
        loading={loading}
        disabled={disabled}
        onClick={onContinue}
      >
        {t('continue')}
      </Button>
      <Button
        color="primary"
        size="big"
        loading={loading}
        disabled={disabled}
        onClick={() =>
          navigate(RoutePath.Tariffs, { state: { hasOneMonthDiscount: true } })
        }
      >
        {t('activate-discount')}
      </Button>
    </div>
  );
};
