import { useTranslation } from 'react-i18next';
import { PopupWrapper } from '@popup/project/youboost/components/shared/PopupWrapper/PopupWrapper';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { FC } from 'react';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

interface Props {
  onClose: () => void;
}

export const ExitPopup: FC<Props> = ({ onClose }) => {
  const { t } = useTranslation('shared', { keyPrefix: 'exit-popup' });
  const navigate = useNavigate();

  const { handleLogout } = usePremiumData();

  const logoutHandler = async () => {
    navigate(RoutePath.Root);
    await handleLogout();
  };

  return (
    <PopupWrapper
      footer={
        <>
          <Button size={'default'} color={'primary'} onClick={logoutHandler}>
            {t('action-yes')}
          </Button>
          <Button size={'default'} color={'secondary'} onClick={onClose}>
            {t('action-no')}
          </Button>
        </>
      }
      onClose={onClose}
    >
      <h3>{t('title')}</h3>
    </PopupWrapper>
  );
};
