import { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';

interface Props {
  clickHandler(): Promise<void>;
}

export const NegativeFeedbackFooter: FC<Props> = ({ clickHandler }) => {
  const { t } = useTranslation('youboost', { keyPrefix: 'root.feedback' });

  return (
    <Button size={'default'} color={'primary'} onClick={clickHandler}>
      {t('negative.action')}
    </Button>
  );
};
