import { FC } from 'react';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useTranslation } from 'react-i18next';

interface Props {}

export const BackButton: FC<Props> = ({}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'settings-page',
  });
  const navigate = useNavigate();

  const clickHandler = () => {
    navigate(RoutePath.Root);
  };

  return (
    <Button
      size={'default'}
      color={'tertiary'}
      onClick={clickHandler}
      data-close="close-button"
    >
      {t('button-back')}
    </Button>
  );
};
