import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { supportedLngs, resources, isSupportedLanguage } from './languages';
import type { SupportedLanguages } from './types';
import { setLocale } from '@popup/shared/i18n/utils';

export const PRIORITY_LANGUAGE: SupportedLanguages = 'ru';

const lastSettedLocale = localStorage.getItem(
  'i18nextLng',
) as SupportedLanguages | null;

i18n.on('languageChanged', async (language) => {
  if (!isSupportedLanguage(language)) return;

  await setLocalStorageData({
    [LocalStorageKey.CurrentLocale]: language,
  });
});

i18n.use(LanguageDetector).use(initReactI18next).init({
  supportedLngs,
  fallbackLng: supportedLngs,
  debug: true,
  defaultNS: 'shared',
  resources,
});

await setLocale({
  lastSettedLocale,
  currentLanguage: i18n.language as SupportedLanguages,
  priorityLanguage: PRIORITY_LANGUAGE,
  changeLanguage: (language) => {
    void i18n.changeLanguage(language);
  },
});

export { supportedLngs, resources, isSupportedLanguage };
export default i18n;
