import { FC } from 'react';
import { LocalStorageData } from '@app/storage/local/types/sharedTypes';
import s from './FreeData.module.css';
import { Tag } from '@popup/project/youboost/components/shared/Tag/ui/Tag';
import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useLocation, useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

interface Props {}

const list = [
  { text: 'list-item-1' },
  { text: 'list-item-2' },
  { text: 'list-item-3' },
  { text: 'list-item-4' },
] as const;

export const FreeData: FC<Props> = ({}) => {
  const navigate = useNavigate();
  const location = useLocation();

  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page.free',
  });

  const clickHandler = () => {
    navigate(RoutePath.Tariffs, {
      state: { prevRoute: location.pathname },
    });
  };

  return (
    <div className={s.wrapper}>
      <div className={s.top}>
        <div className={s.box}>
          <div className={s.title}>{t('title')}</div>
          <Tag>Free</Tag>
        </div>
        <div className={s.list}>
          {list.map((item) => (
            <div key={item.text} className={s.item}>
              {t(item.text)}
            </div>
          ))}
        </div>
      </div>
      <div className={s.footer}>
        <Button size={'default'} color={'primary'} onClick={clickHandler}>
          {t('buy-premium')}
        </Button>
      </div>
    </div>
  );
};
