import { FC } from 'react';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import s from './ServerChanged.module.css';
import { Trans, useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { CheckCircleBroken } from '@popup/project/youboost/components/shared/icons';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

interface Props {}

export const ServerChanged: FC<Props> = ({}) => {
  const [isAllInternetProxy] = useLocalStorage(
    LocalStorageKey.IsAllInternetProxy,
  );

  const { t } = useTranslation('youboost', { keyPrefix: 'server-changed' });
  const { t: modeSwitcherT } = useTranslation('youboost', {
    keyPrefix: 'mode-switcher',
  });

  const navigate = useNavigate();

  const isYoutubeMode = !Boolean(isAllInternetProxy);

  const currentMode = modeSwitcherT(
    isYoutubeMode ? 'youtube-only' : 'all-internet',
  );
  const secondMode = modeSwitcherT(
    isYoutubeMode ? 'all-internet' : 'youtube-only',
  );

  return (
    <SecondaryWrapper
      header={t('title')}
      footer={
        <Button
          color="secondary"
          size="default"
          onClick={() => navigate(RoutePath.Root)}
        >
          {t('ok-btn')}
        </Button>
      }
      closeClickHandler={() => navigate(RoutePath.Root)}
    >
      <div className={s.body}>
        <CheckCircleBroken className={s.icon} />
        <div className={s.main}>
          <div className={s.title}>
            <Trans
              t={t}
              i18nKey="text"
              components={{ br: <br /> }}
              values={{ mode: currentMode }}
            />
          </div>
          <div className={s.description}>
            <Trans
              t={t}
              i18nKey="description"
              components={{ br: <br /> }}
              values={{ mode1: currentMode, mode2: secondMode }}
            />
          </div>
        </div>
      </div>
    </SecondaryWrapper>
  );
};
