import { FetchTariffsResponse, TariffData } from '@api/urls';
import { LimitType } from '@popup/project/youboost/components/TariffsContent/types';

export const getDeviceLimits = (
  tariffs: FetchTariffsResponse,
): Array<LimitType> => [
  ...new Set(tariffs.map((tariff) => tariff.deviceLimit)),
];

export const getTariffs = (
  tariffs: FetchTariffsResponse,
  selectedLimit: LimitType,
) =>
  tariffs
    .filter(({ deviceLimit }) => deviceLimit === selectedLimit)
    .sort((a, b) => a.unlockedDays - b.unlockedDays);

export const getTariffLocaleKey = (tariffCode: TariffData['tariff']) => {
  const tariffCodeFragments = tariffCode.split('_');

  if (tariffCodeFragments.length <= 2) {
    return tariffCode;
  }
  return tariffCodeFragments.slice(0, 2).join('_');
};

export const findTariffByDays = (
  tariffList: TariffData[],
  days: TariffData['unlockedDays'],
): TariffData => {
  const foundTariff = tariffList.find((item) => item.unlockedDays === days);
  return foundTariff ?? tariffList[tariffList.length - 1];
};

export const convertPriceToEuro = (price?: number, rate?: number) => {
  if (!price || !rate) return null;
  return parseFloat((price / rate).toFixed(2));
};
