import { Trans, useTranslation } from 'react-i18next';
import { useGiftAdvertisingContent } from '../hooks/useGiftAdvertisingContent';
import styles from './GiftAdvertisingContent.module.css';
import { TelegramIcon } from '@popup/shared/icons/TelegramIcon';
import icon from '@popup/project/youboost/assets/icon.png';
import { Footer } from '../../shared/Footer';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import { useLocation, useNavigate } from 'react-router-dom';
import { Button } from '../../shared/Button';

type StateType =
  | {
      imageUrl: string;
    }
  | undefined;

export function GiftAdvertisingContent() {
  const location = useLocation();
  const navigate = useNavigate();
  const { t } = useTranslation('youboost', {
    keyPrefix: 'gift-advertising',
  });

  const state = location.state as StateType;
  const imageUrl = state?.imageUrl ?? icon;

  const { onClickHideScreen, onClickTelegramButton, validUntil } =
    useGiftAdvertisingContent();

  return (
    <SecondaryWrapper
      header={t('title')}
      footer={<Footer className={styles.footer} />}
      backButtonProps={{
        onClick: () => navigate(-1),
      }}
      closeClickHandler={() => navigate(RoutePath.Root)}
    >
      <div className={styles.header}>
        <div className={styles.iconContainer}>
          <img className={styles.iconImage} src={imageUrl} alt="icon" />
        </div>
        <div className={styles.title}>
          <Trans t={t} i18nKey="text" components={{ tag: <br /> }} />
        </div>
      </div>
      <div className={styles.body}>
        <div className={styles.description}>
          <Trans
            t={t}
            i18nKey="description"
            components={{ tag: <br />, tag2: <span>{validUntil}</span> }}
          />
        </div>
        <div className={styles.buttons}>
          <Button size={'big'} color={'primary'} onClick={onClickHideScreen}>
            {t('ok-button')}
          </Button>
          <Button
            size={'big'}
            color={'secondary'}
            onClick={onClickTelegramButton}
          >
            <TelegramIcon className={styles.telegramIcon} /> {t('tg-button')}
          </Button>
        </div>
      </div>
    </SecondaryWrapper>
  );
}
