import { FC, useMemo } from 'react';
import { clsx } from 'clsx';
import { Trans, useTranslation } from 'react-i18next';
import type { LocalStorageData } from '@app/storage/local/types/sharedTypes';
import s from './LeftTime.module.css';
import dayjs from 'dayjs';

interface Props {
  daysLeft: NonNullable<LocalStorageData['daysLeft']>;
  secondsLeft: NonNullable<LocalStorageData['secondsLeft']>;
  paidUntil: LocalStorageData['paidUntil'];
}

export const LeftTime: FC<Props> = ({ daysLeft, secondsLeft, paidUntil }) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page.premium',
  });

  const { displayKey, timeValue, shouldShake } = useMemo(() => {
    if (daysLeft > 1 || !secondsLeft) {
      return {
        displayKey: 'days-remaining',
        timeValue: daysLeft,
        shouldShake: daysLeft <= 1,
      };
    }

    const hours = Math.ceil(secondsLeft / 3600);
    return {
      displayKey: 'hours-remaining',
      timeValue: hours,
      shouldShake: hours <= 3,
    };
  }, [daysLeft, secondsLeft]);

  return (
    <div className={s.wrapper} data-testid="left-time">
      <div className={clsx(s.title, shouldShake && s.shake)}>
        <Trans
          t={t}
          i18nKey={displayKey}
          components={{ time: <span /> }}
          count={timeValue}
        />
      </div>
      {paidUntil && (
        <div className={s.label}>
          {t('until', { date: dayjs(paidUntil).format('DD.MM.YY HH:mm') })}
        </div>
      )}
    </div>
  );
};
