import { Trans, useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { Error } from '@popup/project/youboost/components/shared/errors/Error';
import { Prohibited } from '@popup/shared/icons/Prohibited';
import { BaseRoutePath } from '@popup/shared/routes/types/RoutePath';
import { useChangeDevice } from '../hooks/useChangeDevice';
import { LoadingScreen } from '../../shared/LoadingScreen/LoadingScreen';
import styles from './ChangeDeviceContent.module.css';

export function ChangeDeviceContent(): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'change-device',
  });

  const {
    handleCancelChangeDevice,
    handleChangeDevice,
    isChangeDeviceError,
    isChangeDeviceLoading,
  } = useChangeDevice(BaseRoutePath.Root);

  if (isChangeDeviceLoading) {
    return <LoadingScreen title={t('loader-title')} text={t('loader-text')} />;
  }

  if (isChangeDeviceError) {
    return <Error />;
  }

  return (
    <div className={styles.wrapper}>
      <Error
        onClose={handleCancelChangeDevice}
        icon={<Prohibited className={styles.icon} />}
        title={t('title')}
        subtitle={t('subtitle')}
        text={<Trans t={t} i18nKey="description" />}
        footer={
          <div className={styles.buttons}>
            <Button
              onClick={handleChangeDevice}
              color="secondary"
              size="default"
            >
              {t('actions.use-here')}
            </Button>
            <Button
              onClick={handleCancelChangeDevice}
              color="tertiary"
              size="default"
            >
              {t('actions.logout')}
            </Button>
          </div>
        }
      />
    </div>
  );
}
