import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useConnectButton } from '@popup/shared/components/ConnectButton/hooks/useConnectButton';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { ConnectButton } from '@popup/project/youboost/components/ConnectButton';
import s from './ProxyConnect.module.css'

export function ProxyConnect(): JSX.Element {
  const paymentStatus = usePaymentStatus();
  const { hasPremium } = usePremiumData();

  const { handleClick, status } = useConnectButton({
    onExpiredRoute: RoutePath.Tariffs,
  });

  return (
    <div className={s.wrapper} data-testid="proxy-connect">
      <ConnectButton
        status={status}
        onClick={handleClick}
        disabled={
          (paymentStatus === 'Expired' || paymentStatus === 'New') && hasPremium
        }
      />
    </div>
  );
}
