import clsx from 'clsx';
import { useTranslation } from 'react-i18next';
import styles from './DiscountPriceText.module.css';

type DiscountPriceTextProps = {
  className?: string;
  discountPrice?: number;
};

export const DiscountPriceText = ({
  className,
  discountPrice,
}: DiscountPriceTextProps) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'common',
  });

  return (
    <p className={clsx(styles['discount-price-text'], className)}>
      {t('price', { price: discountPrice ?? '~' })}
    </p>
  );
};
