import { Trans, useTranslation } from 'react-i18next';
import { Dropdown } from '@popup/project/youboost/components/shared/Dropdown/ui/Dropdown';
import {
  BaseOption,
  TypedSelectProps,
} from '@popup/project/youboost/components/shared/Dropdown/types';
import { Tooltip } from '@popup/project/youboost/components/shared/Tooltip';
import styles from './SettingsItem.module.css';

interface Props<Option extends BaseOption = BaseOption> {
  i18nKey: string;
  dropdownProps?: TypedSelectProps<Option>;
  'data-testid'?: string;
  children?: React.ReactNode;
  isDisabled?: boolean;
}

export const SettingsItem = <Option extends BaseOption = BaseOption>({
  i18nKey,
  dropdownProps,
  'data-testid': dataTestId,
  children,
  isDisabled,
}: Props<Option>) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'settings-page',
  });

  return (
    <div className={styles.item} data-testid={dataTestId}>
      <div className={styles.text}>
        <Trans t={t} i18nKey={i18nKey} components={{ span: <span /> }} />
      </div>
      {dropdownProps && (
        <div data-tooltip-id={`premium-tooltip-${i18nKey}`}>
          <Dropdown<Option> {...dropdownProps} isDisabled={isDisabled} />
          {isDisabled && (
            <Tooltip
              place={'left'}
              id={`premium-tooltip-${i18nKey}`}
              content={t('disabled-tooltip')}
            />
          )}
        </div>
      )}
      {children}
    </div>
  );
};
