import { useCallback, useEffect, useState } from 'react';
import { FetchTariffsResponse, TariffData } from '@api/urls';
import { LimitType } from '@popup/project/youboost/components/TariffsContent/types';
import {
  findTariffByDays,
  getTariffs,
} from '@popup/project/youboost/components/TariffsContent/utils';
import { Tariff } from '@api/premium/types/Tariff';

interface UseTariffSelectionProps {
  tariffs: FetchTariffsResponse;
  selectedLimit: LimitType;
  hasOneMonthDiscount: boolean;
}

interface UseTariffSelectionReturn {
  tariffsByLimit: TariffData[];
  selectedTariff: TariffData;
  handleTariffSelect: (tariff: Tariff) => void;
}

export const useTariffSelection = ({
  tariffs,
  selectedLimit,
  hasOneMonthDiscount,
}: UseTariffSelectionProps): UseTariffSelectionReturn => {
  const tariffsByLimit = getTariffs(tariffs, selectedLimit);

  const [selectedTariff, setSelectedTariff] = useState<TariffData>(
    hasOneMonthDiscount
      ? tariffsByLimit[0]
      : tariffsByLimit[tariffsByLimit.length - 1],
  );

  useEffect(() => {
    const newTariffData = getTariffs(tariffs, selectedLimit);
    setSelectedTariff((prev) =>
      findTariffByDays(newTariffData, prev.unlockedDays),
    );
  }, [tariffs, selectedLimit]);

  const handleTariffSelect = useCallback(
    (tariff: Tariff) => {
      const foundTariff = tariffsByLimit.find((item) => item.tariff === tariff);
      if (foundTariff) {
        setSelectedTariff(foundTariff);
      }
    },
    [tariffsByLimit],
  );

  return {
    tariffsByLimit,
    selectedTariff,
    handleTariffSelect,
  };
};
