import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

import styles from './FooterButtons.module.css';

export const FooterButtons = ({
  onCancelPremium,
}: {
  onCancelPremium: () => void;
}): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.discount.actions',
  });
  const navigate = useNavigate();

  return (
    <div className={styles.buttons}>
      <Button color="tertiary" size="default" onClick={onCancelPremium}>
        {t('money-back')}
      </Button>
      <Button
        color="primary"
        size="default"
        onClick={() =>
          navigate(RoutePath.Tariffs, { state: { hasOneMonthDiscount: true } })
        }
      >
        {t('activate-discount')}
      </Button>
    </div>
  );
};
