import { detect } from 'detect-browser';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import JSZip from 'jszip';
import dayjs from 'dayjs';
import { reducePacScript } from '@popup/shared/helpers/reducePacScript';

type DebugButtonHookValues = {
  isDownloaded: boolean;
  handleDownload: () => void;
};

export function useDebugButton(): DebugButtonHookValues {
  const [isDownloaded, setIsDownloaded] = useState(false);
  const timeoutRef = useRef<NodeJS.Timeout | null>(null);

  const handleDownload = useCallback(async () => {
    try {
      setIsDownloaded(true);

      const browserInfo = detect();
      const extensionInfo = {
        version: browser_api.runtime.getManifest().version,
      };
      const platformInfo = await browser_api.runtime.getPlatformInfo();
      const storageData = {
        local: reducePacScript(await getLocalStorageData()),
      };

      const debugData = JSON.stringify(
        {
          browserInfo,
          extensionInfo,
          platformInfo,
          storageData,
          chromeRuntimeLastError: browser_api.runtime.lastError?.message,
        },
        null,
        2,
      );

      const encodedDebugData = btoa(encodeURIComponent(debugData));
      const fileName = `debug-info-${dayjs().format('YYYY-MM-DD_HH-mm-ss')}`;

      const zip = new JSZip();
      zip.file(`${fileName}.txt`, encodedDebugData);

      const blob = await zip.generateAsync({ type: 'blob' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `${fileName}.zip`;
      a.click();
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('Error gathering debug information:', error);
    } finally {
      setIsDownloaded(true);
      timeoutRef.current = setTimeout(() => setIsDownloaded(false), 1000);
    }
  }, []);

  useEffect(() => {
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);

  return useMemo(
    () => ({
      isDownloaded,
      handleDownload,
    }),
    [handleDownload, isDownloaded],
  );
}
