import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import log, { LoggingMethod, LogLevelNames, LogLevelNumbers } from 'loglevel';

const originalFactory = log.methodFactory;

function isLogExpired(timestamp: string) {
  const logsPeriodExpiration = process.env.LOGS_PERIOD_EXPIRATION
    ? parseInt(process.env.LOGS_PERIOD_EXPIRATION, 10)
    : 1000 * 60 * 60 * 24; // 1 day;

  const logTimestamp = new Date(timestamp).getTime();
  const now = new Date().getTime();

  return now - logTimestamp > logsPeriodExpiration;
}

function methodFactory(
  methodName: LogLevelNames,
  logLevel: LogLevelNumbers,
  loggerName: string | symbol,
): LoggingMethod {
  const isLoggerEnabled = process.env.IS_LOGGER_ENABLED === 'true';

  const rawMethod = originalFactory(methodName, logLevel, loggerName);
  const getTimestamp = () => new Date().toISOString();

  return function (...args: any[]): void {
    if (isLoggerEnabled) {
      rawMethod(getTimestamp(), ...args);
    }

    return void setLogs(
      getTimestamp(),
      args
        .map((arg) => {
          if (typeof arg !== 'string') {
            return JSON.stringify(arg);
          }
          return arg;
        })
        .join(' '),
    );
  };
}

async function setLogs(timestamp: string, message: string) {
  const { logs } = await getLocalStorageData(LocalStorageKey.Logs);

  const filteredLogs =
    logs?.filter((log) => !isLogExpired(log.timestamp)) || [];

  await setLocalStorageData({
    [LocalStorageKey.Logs]: [...filteredLogs, { timestamp, message }].slice(-200),
  });
}

export const logger = log;

export function setLogLevel(
  level: 'trace' | 'debug' | 'info' | 'warn' | 'error' | 'silent',
) {
  log.methodFactory = methodFactory;
  log.rebuild();
  log.setLevel(level, true);
}

export function enableLogger() {
  log.setLevel('debug', true);
}

export function disableLogger() {
  log.setLevel('silent', true);
}

export function toggleLogger() {
  log.getLevel() !== 5 ? disableLogger() : enableLogger();
}

export function getIsLoggerEnabled() {
  return log.getLevel() !== 5;
}

setLogLevel('debug');
