import { FC, ReactNode, useRef, useState, MouseEvent } from 'react';
import { useLocation } from 'react-router-dom';
import { CSSTransition, SwitchTransition } from 'react-transition-group';
import styles from './TransitionWrapper.module.css';

interface Props {
  children: ReactNode;
}

const closeSelectors = ['[data-close="close-button"]'];

export const TransitionWrapper: FC<Props> = ({ children }) => {
  const [isReversed, setIsReversed] = useState(false);
  const location = useLocation();
  const ref = useRef(null);

  const handleMouseDown = (event: MouseEvent<HTMLDivElement>): void => {
    const target = event.target as HTMLElement;
    const isClose = closeSelectors.some((sel) => target.closest(sel));
    setIsReversed(isClose);
  };

  const classNames = {
    enter: styles[`page-enter${isReversed ? '-reversed' : ''}`],
    enterActive: styles[`page-enter-active${isReversed ? '-reversed' : ''}`],
    exit: styles[`page-exit${isReversed ? '-reversed' : ''}`],
    exitActive: styles[`page-exit-active${isReversed ? '-reversed' : ''}`],
  };

  return (
    <div className={styles.wrapper} onMouseDown={handleMouseDown}>
      <SwitchTransition>
        <CSSTransition
          className={styles.content}
          key={location.key}
          unmountOnExit
          nodeRef={ref}
          timeout={350}
          classNames={classNames}
        >
          <div ref={ref} data-testid="transition-content">
            {children}
          </div>
        </CSSTransition>
      </SwitchTransition>
    </div>
  );
};
