import { FC, useRef } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper/ui/SecondaryWrapper';
import { useTranslation } from 'react-i18next';
import {
  Body,
  useTariffs,
} from '@popup/project/youboost/components/TariffsContent';
import type { TariffData } from '@api/urls';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { Error } from '@popup/project/youboost/components/shared/errors/Error';
import { LoadingScreen } from '@popup/project/youboost/components/shared/LoadingScreen/LoadingScreen';

type StateType =
  | {
      hasOneMonthDiscount?: boolean;
      prevRoute?: string;
    }
  | undefined;

export const Tariffs: FC = () => {
  const { isTariffError, isTariffsLoading, tariffs, discountPrice } =
    useTariffs();

  const navigate = useNavigate();
  const location = useLocation();

  const selectedTariffRef = useRef<TariffData | null>(null);

  const { t } = useTranslation('youboost', { keyPrefix: 'tariffs-page' });

  const [hasPremium] = useLocalStorage(LocalStorageKey.HasPremium);

  const extend = Boolean(hasPremium);
  const state = location.state as StateType;
  const hasOneMonthDiscount = Boolean(state?.hasOneMonthDiscount);
  const prevRoute = state?.prevRoute ?? RoutePath.Root;

  const continueHandler = () => {
    const selectedTariff = selectedTariffRef.current;
    if (!selectedTariff) return;

    navigate(RoutePath.Payment, {
      state: {
        selectedTariff,
        hasOneMonthDiscount,
        prevRoute,
        discountPrice,
      },
    });
  };

  const closeClickHandler = () => {
    navigate(RoutePath.Root);
  };
  const backClickHandler = () => {
    navigate(prevRoute);
  };

  if (isTariffError) {
    return <Error />;
  }

  if (isTariffsLoading) {
    return <LoadingScreen title={t('loader-title')} text={t('loader-text')} />;
  }

  if (!tariffs) return null;

  return (
    <SecondaryWrapper
      header={t(extend ? 'title_extend' : 'title_buy')}
      footer={
        <Button size={'default'} color={'primary'} onClick={continueHandler}>
          {t('continue')}
        </Button>
      }
      closeClickHandler={closeClickHandler}
      backButtonProps={{
        onClick: backClickHandler,
      }}
    >
      <Body
        selectedTariffRef={selectedTariffRef}
        tariffs={tariffs}
        hasOneMonthDiscount={hasOneMonthDiscount}
        discountPrice={discountPrice}
      />
    </SecondaryWrapper>
  );
};
