import { useEffect } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

import { useGetConvertedDiscountPrice } from '@popup/project/youboost/hooks/useGetConvertedDiscountPrice';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useSetupTariffFree } from '@popup/shared/hooks/useSetupTariffFree';
import { DiscountPriceText } from '../../shared/DiscountPriceText';
import { Error } from '../../shared/errors/Error';
import { FooterButtons } from './FooterButtons/FooterButtons';

export const PremiumSalePersuasionContent = () => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'premium-sale-persuasion',
  });
  const { t: tCommon } = useTranslation('youboost', {
    keyPrefix: 'common',
  });

  const { price, discountPrice } = useGetConvertedDiscountPrice();

  const navigate = useNavigate();

  const { handleClickBecomeFreeUser, isLoading, isSuccess, isError } =
    useSetupTariffFree();

  useEffect(() => {
    if (isSuccess) {
      navigate(RoutePath.Root, { replace: true });
    }
  }, [isSuccess, navigate]);

  if (isError) {
    return (
      <Error
        text={t('error')}
        onClose={() => {
          navigate(RoutePath.Root);
        }}
      />
    );
  }

  return (
    <Error
      title={t('title')}
      icon={
        <DiscountPriceText
          discountPrice={discountPrice ? discountPrice : undefined}
        />
      }
      subtitle={t('subtitle')}
      text={
        <Trans
          t={t}
          i18nKey="description"
          values={{
            price: tCommon('price', { price: price ?? '~' }),
            discountPrice: tCommon('price', { price: discountPrice ?? '~' }),
          }}
        />
      }
      footer={
        <FooterButtons
          disabled={isLoading}
          loading={isLoading}
          onContinue={handleClickBecomeFreeUser}
        />
      }
      onClose={() => {
        navigate(RoutePath.Root);
      }}
    />
  );
};
