import clsx from 'clsx';
import { forwardRef, useCallback, useEffect } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { setLocalStorageData } from '@app/storage/local/shared';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { Header } from '@popup/project/youboost/components/shared/Header';
import { Input } from '@popup/project/youboost/components/shared/Input';
import { UserEditIcon } from '@popup/project/youboost/components/shared/icons';
import { useEnterIdForm } from '@popup/shared/components/EnterIdForm/hooks/useEnterIdForm';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';

import { OnboardingStep } from '../../constants';
import { StepProps } from '../../types';

import styles from './EnterIdStep.module.css';

export const EnterIdStep = forwardRef<HTMLDivElement, StepProps>(
  ({ onStepChange }, ref) => {
    const { t } = useTranslation('youboost', {
      keyPrefix: 'onboarding.enter-id-step',
    });

    const {
      enteredId,
      handleChange,
      handleSubmit,
      inputRef,
      isError,
      isLoading,
      clearError,
      isIdSettled,
    } = useEnterIdForm();

    const paymentStatus = usePaymentStatus();

    const handleBackButtonClick = useCallback(() => {
      onStepChange(0);
    }, [onStepChange]);

    const handleForgetButtonClick = useCallback(() => {
      onStepChange(2);
    }, [onStepChange]);

    useEffect(() => {
      async function updateOnboardingScreenStatus() {
        await setLocalStorageData({
          onboardingScreenStatus: OnboardingStep.Done,
        });
      }

      if (isIdSettled && paymentStatus) {
        updateOnboardingScreenStatus();
      }
    }, [isIdSettled, paymentStatus]);

    return (
      <div
        ref={ref}
        className={styles.container}
        data-testid="enter-id-step"
        data-theme="default"
      >
        <Header />
        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.content}>
            <UserEditIcon className={styles.icon} />
            <h1 className={styles.title}>{t('title')}</h1>
            <div className={styles['input-container']}>
              {isError && <div className={styles.error}>{t('id-error')}</div>}
              <Input
                className={clsx(styles.input, {
                  [styles['input-error']]: isError,
                })}
                ref={inputRef}
                value={enteredId}
                onChange={handleChange}
                placeholder={t('id-input-placeholder')}
                onFocus={clearError}
                disabled={isLoading}
                name="id-input"
              />

              <button
                onClick={handleForgetButtonClick}
                className={styles.link}
                type="button"
              >
                <Trans t={t} i18nKey="forget-id" />
              </button>
            </div>
          </div>
          <div className={styles.buttons}>
            <Button
              onClick={handleSubmit}
              className={styles.submit}
              type="submit"
              disabled={isLoading}
              loading={isLoading}
              color="secondary"
              size="big"
            >
              {isLoading ? t('id-checking') : t('id-submit')}
            </Button>
            <Button
              className={styles.button}
              color="tertiary"
              onClick={handleBackButtonClick}
              disabled={isLoading}
              size={'big'}
            >
              {t('back-button')}
            </Button>
          </div>
        </form>
      </div>
    );
  },
);

EnterIdStep.displayName = 'EnterIdStep';
