import { useEffect, useLayoutEffect, useState } from 'react';
import { type Theme } from '../ThemeContext';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { SelectedTheme } from '@app/storage/local/types/persistentTypes';

const POSSIBLE_THEMES: Theme[] = ['default', 'premium'];

export function useThemeContext(): {
  theme: Theme;
  isThemeLoading: boolean;
  possibleThemes: Theme[];
  switchTheme: (theme: string) => void;
} {
  const [selectedTheme, setSelectedTheme] = useState<SelectedTheme | null>(
    null,
  );
  const [theme, setTheme] = useState<Theme>('premium');
  const [isThemeLoading, setIsThemeLoading] = useState(true);
  const paymentStatus = usePaymentStatus();

  useEffect(() => {
    (async () => {
      const { selectedTheme } = await getLocalStorageData(
        LocalStorageKey.SelectedTheme,
      );
      if (selectedTheme) {
        setSelectedTheme(selectedTheme);
      }
    })();
  }, []);

  useEffect(() => {
    if (paymentStatus !== undefined) {
      if (selectedTheme && paymentStatus !== 'Free') {
        setTheme(selectedTheme);
      } else {
        setTheme(paymentStatus === 'Free' ? 'default' : 'premium');
      }
      setIsThemeLoading(false);
    }
  }, [paymentStatus, selectedTheme]);

  useEffect(() => {
    const code = process.env.EXTENSION_CODE;
    if (code === 'youboost' && paymentStatus === undefined) {
      setTheme('default');
      setIsThemeLoading(false);
    }
  }, [paymentStatus]);

  useLayoutEffect(() => {
    document.body.classList.toggle('premium', theme === 'premium');
    /*       'data-theme' */
    document.documentElement.setAttribute('data-theme', theme);
  }, [theme]);

  const switchTheme = (theme: string) => {
    if (theme === 'default') {
      setSelectedTheme('default');
      void setLocalStorageData({ [LocalStorageKey.SelectedTheme]: 'default' });
      return;
    }
    if (theme === 'premium') {
      setSelectedTheme('premium');
      void setLocalStorageData({ [LocalStorageKey.SelectedTheme]: 'premium' });
      return;
    }
    void setLocalStorageData({ [LocalStorageKey.ConfigsBaseUrl]: undefined });
  };

  return {
    theme,
    isThemeLoading,
    possibleThemes: POSSIBLE_THEMES,
    switchTheme,
  };
}
