import { Dispatch, FC, SetStateAction } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Tabs,
  type TabItem,
} from '@popup/project/youboost/components/shared/Tabs';
import s from './SelectDeviceLimit.module.css';
import { LimitType } from '@popup/project/youboost/components/TariffsContent/types';
import { BrowserCountTabItem } from '@popup/project/youboost/components/TariffsContent/ui/BrowserCountTabItem/BrowserCountTabItem';

interface Props {
  deviceLimits: LimitType[];
  selectedLimit: LimitType;
  setSelectedLimit: Dispatch<SetStateAction<LimitType>>;
}

const getDeviceLimitItems = ({
  selectedLimit,
  deviceLimits,
}: {
  selectedLimit: LimitType;
  deviceLimits: LimitType[];
}): TabItem<LimitType>[] => {
  return deviceLimits.map((limit) => {
    const id = limit;
    return {
      id,
      label: (
        <BrowserCountTabItem isActive={selectedLimit === id} count={limit} />
      ),
      content: null,
      dataTestId: id,
    };
  });
};

export const SelectDeviceLimit: FC<Props> = ({
  selectedLimit,
  deviceLimits,
  setSelectedLimit,
}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'tariffs-page',
  });

  const deviceLimitItems = getDeviceLimitItems({
    selectedLimit,
    deviceLimits,
  });

  return (
    <div className={s.box}>
      <div className={s.header}>
        <div className={s.title}>{t('browser-amount-title')}</div>
        <div className={s.label}>{t('browser-amount-label')}</div>
      </div>
      <Tabs<LimitType>
        items={deviceLimitItems}
        onChangeActiveTab={setSelectedLimit}
        activeTabId={selectedLimit}
        waitMS={300}
      />
    </div>
  );
};
