import React, { useLayoutEffect, useRef, useState } from 'react';
import { motion } from 'motion/react';
import {
  Orientation,
  TabItem,
} from '@popup/project/youboost/components/shared/Tabs/types';
import s from './Tabs.module.css';
import clsx from 'clsx';

interface Props<T extends string | number> {
  items: TabItem<T>[];
  activeTabId: T;
  onChangeActiveTab: (tabId: T) => void;
  orientation?: Orientation;
  waitMS?: number;
  itemClassName?: string;
}

export const Tabs = <T extends string | number>({
  items,
  activeTabId,
  onChangeActiveTab,
  itemClassName,
  orientation = 'horizontal',
  waitMS = 0,
}: Props<T>) => {
  const tabRefs = useRef<(HTMLButtonElement | null)[]>([]);
  const [activeRect, setActiveRect] = useState(
    orientation === 'horizontal' ? { x: 0, width: 0 } : { y: 0, height: 0 },
  );

  const activeContent = items.find((item) => item.id === activeTabId)?.content;
  const activeIndex = items.findIndex((item) => item.id === activeTabId);

  useLayoutEffect(() => {
    const el = tabRefs.current[activeIndex];
    if (el) {
      setTimeout(() => {
        const rect =
          orientation === 'horizontal'
            ? {
                x: el.offsetLeft,
                width: el.offsetWidth,
              }
            : {
                y: el.offsetTop,
                height: el.offsetHeight,
              };
        setActiveRect(rect);
      }, waitMS);
    }
  }, [activeIndex, waitMS, orientation]);

  return (
    <div>
      <div
        role="tablist"
        className={clsx(s.tabs, orientation === 'vertical' && s.tabs_vertical)}
      >
        <motion.div
          className={s.decor}
          layout
          transition={{
            type: 'spring',
            visualDuration: 0.2,
            bounce: 0.2,
          }}
          animate={activeRect}
        />
        {items.map((item, index) => (
          <motion.button
            key={item.id}
            className={clsx(
              s.item,
              activeTabId === item.id && s.active,
              item.disabled && s.disabled,
              itemClassName,
            )}
            role="tab"
            whileTap={item.disabled ? undefined : { scale: 0.98 }}
            aria-selected={activeTabId === item.id}
            aria-controls={`panel-${item.id}`}
            id={`tab-${item.id}`}
            onClick={() => {
              if(item.disabled) return
              onChangeActiveTab(item.id)
            }}
            data-testid={item.dataTestId}
            ref={(el) => (tabRefs.current[index] = el)}
          >
            {item.label}
          </motion.button>
        ))}
      </div>

      <div
        role="tabpanel"
        id={`panel-${activeTabId}`}
        aria-labelledby={`tab-${activeTabId}`}
      >
        {activeContent}
      </div>
    </div>
  );
};
