import { FC } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import { Puzzle } from '@popup/project/youboost/components/shared/icons/Puzzle/Puzzle';
import { motion } from 'motion/react';
import s from './BrowserCountTabItem.module.css';

interface Props {
  isActive: boolean;
  count: number;
}

export const BrowserCountTabItem: FC<Props> = ({ isActive, count }) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'tariffs-page',
  });

  return (
    <span className={s.item}>
      <Trans
        t={t}
        i18nKey="browser-amount"
        components={{
          icon: <Puzzle className={s.icon} />,
          span: (
            <motion.span
              className={s.text}
              initial={{ width: 0 }}
              animate={{ width: isActive ? 'auto' : 0 }}
              transition={{ duration: 0.3, ease: 'easeInOut' }}
            />
          ),
        }}
        count={count}
      />
    </span>
  );
};
