import React from 'react';
import clsx from 'clsx';

import styles from './Switch.module.css';

type SwitchProps = {
  on: boolean;
  setOn: () => void;
  className?: string;
};

export function Switch({ on, setOn, className }: SwitchProps): JSX.Element {
  return (
    <div className={clsx(styles.wrapper, className)}>
      <div
        className={styles.switch}
        onClick={setOn}
        role="switch"
        aria-checked={on}
      >
        <div
          className={clsx(
            styles.circle,
            { [styles.on]: on },
            { [styles.off]: !on },
          )}
        />
      </div>
    </div>
  );
}
