import { httpClient } from './httpClient';

type UserCountry = {
  country: string;
};

type UserCountryResponse = {
  data: UserCountry;
  error: string | null;
};

export async function fetchUserCountry({
  apiBaseUrl,
}: {
  apiBaseUrl: string;
}): Promise<UserCountry> {
  try {
    const response = await httpClient
      .get<UserCountryResponse>(
        `https://${apiBaseUrl}/client-api/v1/get-country`,
      )
      .json();

    if (response.error) {
      throw new Error(response.error);
    }

    return response.data;
  } catch (error) {
    console.error('User country data fetch error', error);
    throw error;
  }
}
