import shared from './shared.json';
import { Resource } from 'i18next';
import content from './content.json';

const EXTENSION = process.env.EXTENSION_CODE;

const ru: Resource = {
  shared,
  content,
};

if (EXTENSION === 'youboost-neo') {
  ru['youboost-neo'] = require('./youboost-neo.json');
} else if (EXTENSION === 'youboost') {
  ru.youboost = require('./youboost.json');
} else if (EXTENSION === 'twihd') {
  ru.twihd = require('./twihd.json');
} else if (EXTENSION === 'otkryvashka') {
  ru.otkryvashka = require('./otkryvashka.json');
} else if (EXTENSION === 'vrubel') {
  ru.vrubel = require('./vrubel.json');
}

export default ru;
