import { BackButton } from '@popup/project/youboost/components/SettingsContent';
import { LogoutButton } from '@popup/project/youboost/components/shared/LogoutButton/ui/LogoutButton';
import { Trans, useTranslation } from 'react-i18next';
import { Logout } from '@popup/project/youboost/components/shared/icons/Logout/Logout';
import styles from './FooterButtons.module.css';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';

export const FooterButtons = () => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'settings-page',
  });

  const { userId } = usePremiumData();

  return (
    <div className={styles['footer-buttons']}>
      {userId && (
        <LogoutButton size={'default'} color={'logout'}>
          <span className={styles.logout}>
            <Trans
              t={t}
              i18nKey={'logout-button'}
              components={{
                icon: <Logout className={styles.logoutIcon} />,
              }}
            />
          </span>
        </LogoutButton>
      )}
      <BackButton />
    </div>
  );
};
