import { Trans, useTranslation } from 'react-i18next';
import styles from './Success.module.css';
import { useNavigate } from 'react-router-dom';
import clsx from 'clsx';
import { Error } from '@popup/project/youboost/components/shared/errors/Error';
import { CancelPage } from '../..';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { CancelSubscriptionStatus } from '@api/urls';

type SuccessProps = {
  page: CancelPage;
  cancelStatus?: CancelSubscriptionStatus;
};

export function Success({ page, cancelStatus }: SuccessProps): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.success',
  });

  const navigate = useNavigate();

  return (
    <div
      className={clsx(styles.wrapper, {
        [styles['out']]: page !== 'success',
      })}
      data-attribute="success"
    >
      <Error
        title={t('title')}
        icon={<div className={styles.icon}></div>}
        subtitle={t('subtitle')}
        text={
          cancelStatus === 'Canceled' ? (
            <Trans t={t} i18nKey="description" />
          ) : (
            ''
          )
        }
        footer={
          <Button
            color="primary"
            size="default"
            onClick={() => navigate(RoutePath.Root)}
          >
            {t('actions.main-screen')}
          </Button>
        }
        onClose={() => {
          navigate(RoutePath.Root);
        }}
      />
    </div>
  );
}
