import { useNavigate } from 'react-router-dom';

import { useFreePremiumTrial } from '@popup/project/youboost/hooks/useFreePremiumTrial';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useTranslation } from 'react-i18next';

import { StatusTag } from '../../shared/StatusTag';
import { CalendarHeartIcon } from '../../shared/icons';

import styles from './SmallPremiumPromo.module.css';
import { LoadingScreen } from '../../shared/LoadingScreen/LoadingScreen';

export function SmallPremiumPromo(): JSX.Element {
  const navigate = useNavigate();
  const { t } = useTranslation('youboost', {
    keyPrefix: 'root.promo',
  });

  const { canTryFreePremiumTrial, isCheckingFreePremiumTrial } =
    useFreePremiumTrial();

  function handleClickPrimary() {
    canTryFreePremiumTrial
      ? navigate(RoutePath.PremiumInfo)
      : navigate(RoutePath.Tariffs);
  }

  function handleClickManageSubscription() {
    navigate(RoutePath.SubscriptionManagement);
  }

  if (isCheckingFreePremiumTrial) {
    return <LoadingScreen title={t('loader-title')} text={t('loader-text')} />;
  }

  return (
    <div className={styles.wrapper}>
      <div className={styles.header}>
        <h3 className={styles.title}>{t('title')}</h3>
        <StatusTag status="Free" />
      </div>
      <ul className={styles['points-container']}>
        <li className={styles.point}>{t('points.1')}</li>
        <li className={styles.point}>{t('points.2')}</li>
        <li className={styles.point}>{t('points.3')}</li>
      </ul>
      <div className={styles.buttons}>
        <Button
          size="default"
          onClick={handleClickPrimary}
          color="primary"
          className={styles.extend}
        >
          {canTryFreePremiumTrial ? t('try-for-free') : t('connect-premium')}
        </Button>

        <Button
          color="secondary"
          size="default"
          onClick={handleClickManageSubscription}
          className={styles.extend}
        >
          {t('manage-subscription')}
          <CalendarHeartIcon className={styles.icon} />
        </Button>
      </div>
    </div>
  );
}
