import { type Resource } from 'i18next';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';
import { SupportedLanguages } from '../types';

const EXTENSION = process.env.EXTENSION_CODE;

const { featuresList } = await featureStore.getSnapshotAsync();
const hasMultiLocales = featuresList[Feature.MultiLocalesSupport];

const getSupportedLngs = (): SupportedLanguages[] => {
  if (!hasMultiLocales) return ['ru'] as const;

  if (EXTENSION === 'youboost') {
    return ['ru', 'en', 'pl', 'es', 'pt', 'de', 'fr', 'tr', 'it'] as const;
  }

  return ['ru', 'en'] as const;
};

const getResources = (): Resource => {
  const { default: ru } = require('@locales/ru');

  if (!hasMultiLocales) return { ru } satisfies Partial<Record<SupportedLanguages, unknown>>;

  const { default: en } = require('@locales/en');

  if (EXTENSION === 'youboost') {
    const { default: pl } = require('@locales/pl');
    const { default: es } = require('@locales/es');
    const { default: pt } = require('@locales/pt');
    const { default: de } = require('@locales/de');
    const { default: fr } = require('@locales/fr');
    const { default: tr } = require('@locales/tr');
    const { default: it } = require('@locales/it');

    return { ru, en, pl, es, pt, de, fr, tr, it } satisfies Record<SupportedLanguages, unknown>;
  }

  return { ru, en } satisfies Partial<Record<SupportedLanguages, unknown>>;
};

export const supportedLngs = getSupportedLngs();
export const resources = getResources();

export const isSupportedLanguage = (value: string): value is SupportedLanguages => {
  return (supportedLngs as string[]).includes(value);
};