import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import clsx from 'clsx';
import { motion } from 'motion/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { ModeSwitcherButton } from './ModeSwitcherButton';

import styles from './ModeSwitcher.module.css';
import { Tooltip } from '../../shared/Tooltip';
import { useLocalStorage } from '@popup/shared/hooks/useLocalStorage';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export type ModeSwitcherProps = {
  hasPremium: boolean;
  isYoutubeMode: boolean;
  onModeChange: (isYoutubeMode: boolean) => void;
};

export const ModeSwitcher: React.FC<ModeSwitcherProps> = ({
  hasPremium,
  isYoutubeMode,
  onModeChange,
}) => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'mode-switcher',
  });

  const [isConnectionButtonDisabled] = useLocalStorage(
    LocalStorageKey.IsConnectionButtonDisabled,
  );

  const paymentStatus = usePaymentStatus();
  const navigate = useNavigate();
  const handleClick = (isYoutubeMode: boolean) => {
    if (isConnectionButtonDisabled) {
      return;
    }

    if (paymentStatus !== 'Paid' && paymentStatus !== 'Trial') {
      navigate(RoutePath.AllInternetPromo);
      return;
    }
    onModeChange(isYoutubeMode);
  };

  return (
    <div className={clsx(styles.container)} data-testid="mode-switcher">
      <ModeSwitcherButton
        dataTestId="mode-switcher-youtube"
        label={t('youtube-only')}
        isActive={isYoutubeMode}
        onClick={() => handleClick(true)}
        disabled={!!isConnectionButtonDisabled}
        notAllowed={!!isConnectionButtonDisabled}
      />
      <ModeSwitcherButton
        dataTestId="mode-switcher-all"
        label={t('all-internet')}
        isActive={!isYoutubeMode}
        disabled={!hasPremium || !!isConnectionButtonDisabled}
        notAllowed={!!isConnectionButtonDisabled}
        onClick={() => handleClick(false)}
        data-tooltip-id="premium-tooltip"
      />
      {!hasPremium && <Tooltip id="premium-tooltip" content={t('tooltip')} />}

      <motion.div
        className={clsx(styles.switcher, {
          [styles['switcher-on']]: !isYoutubeMode,
        })}
        layout
        transition={{
          type: 'spring',
          visualDuration: 0.2,
          bounce: 0.2,
        }}
      />
    </div>
  );
};
