import React, { FC, useState } from 'react';
import s from './Button.module.css';
import { clsx } from 'clsx';
import { ButtonProps } from '@popup/project/youboost/components/shared/Button/types';

interface Props extends ButtonProps {}

export const Button: FC<Props> = ({
  children,
  onClick,
  size,
  color,
  loading,
  disabled,
  className,
  type = 'button',
  ...props
}) => {
  const [isLoading, setIsLoading] = useState(false);

  const clickHandler: Props['onClick'] = async (e) => {
    try {
      setIsLoading(true);
      await onClick(e);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <button
      className={clsx(
        s.button,
        (
          {
            primary: s.primary,
            secondary: s.secondary,
            tertiary: s.tertiary,
            logout: s.logout,
          } satisfies Record<Props['color'], string>
        )[color],
        (
          {
            small: s.small,
            default: s.default,
            big: s.big,
          } satisfies Record<Props['size'], string>
        )[size],
        (loading || isLoading) && s.loading,
        disabled && s.disabled,
      )}
      type={type}
      onClick={clickHandler}
      disabled={loading || isLoading || disabled}
      {...props}
    >
      <span className={clsx(s.body, className)} data-testid={'body'}>
        {children}
      </span>
    </button>
  );
};
