import { useNavigateToChangeServer } from '@popup/project/youboost/hooks/useNavigateToChangeServer';
import { ProxyConnect } from '@popup/project/youboost/components/ProxyConnect';
import { Background } from '@popup/project/youboost/components/RootContent/ui/Background/Background';
import { Header } from '@popup/project/youboost/components/shared/Header';
import { Feedback } from '@popup/project/youboost/components/shared/popups/Feedback';
import { useNavigateToGiftAdvertising } from '@popup/project/youboost/hooks/useNavigateToGiftAdvertising';
import { useNavigateToRefuseGift } from '@popup/project/youboost/hooks/useNavigateToRefuseGift';
import { useNavigateWhenSubscriptionExpiring } from '@popup/project/youboost/hooks/useNavigateWhenSubscriptionExpiring';
import { usePopupReady } from '@popup/project/youboost/hooks/usePopupRedy';
import { useTheme } from '@popup/shared/contexts/Theme';
import { useOnboardingScreen } from '@popup/shared/hooks/useOnboardingScreen';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { ModeSwitcher } from '../../../components/ModeSwitcher';
import { Onboarding } from '../../../components/Onboarding';
import { OnboardingStep } from '../../../components/Onboarding/ui/constants';
import { PremiumData } from '../../../components/PremiumData';
import { SmallPremiumPromo } from '../../../components/SmallPremiumPromo';
import { Footer } from '../../../components/shared/Footer';
import { useChangeDevice } from '../../../hooks/useChangeDevice';
import { useModeSwitcher } from '../../../hooks/useModeSwitcher';
import { useNavigateToPremiumInfo } from '../../../hooks/useNavigateToPremiumInfo';
import { useNavigateToTariffs } from '../../../hooks/useNavigateToTariffs';
import { useNavigateToUnavailableTrial } from '../../../hooks/useNavigateToUnavailableTrial';

import styles from './Root.module.css';

export function Root(): JSX.Element | null {
  const { hasPremium } = usePremiumData();
  const { onboardingStatus, isLoading: isOnboardingStatusLoading } =
    useOnboardingScreen();

  useTheme();
  useChangeDevice();
  useNavigateToPremiumInfo();
  useNavigateToTariffs();
  useNavigateToUnavailableTrial();
  useNavigateToGiftAdvertising();
  useNavigateToRefuseGift();
  useNavigateWhenSubscriptionExpiring();
  useNavigateToChangeServer();
  usePopupReady();

  usePremiumAnimation({ isFreeUser: !hasPremium });

  const { isYoutubeMode, onModeChange } = useModeSwitcher();

  if (isOnboardingStatusLoading) {
    return null;
  }

  if (onboardingStatus !== OnboardingStep.Done) {
    return <Onboarding onboardingStatus={onboardingStatus} />;
  }

  return (
    <div className={styles.wrapper}>
      <Background />
      <div className={styles.header}>
        <Header showLogo />
      </div>
      <div className={styles.main}>
        <ModeSwitcher
          hasPremium={!!hasPremium}
          isYoutubeMode={isYoutubeMode}
          onModeChange={onModeChange}
        />
        <ProxyConnect />
      </div>
      {hasPremium ? <PremiumData /> : <SmallPremiumPromo />}
      <Footer />
      <Feedback />
    </div>
  );
}
