import { FC, useState } from 'react';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { ButtonProps } from '@popup/project/youboost/components/shared/Button/types';
import { createPortal } from 'react-dom';
import { ExitPopup } from '@popup/project/youboost/components/shared/popups/ExitPopup';

interface Props extends Omit<ButtonProps, 'onClick'> {}

export const LogoutButton: FC<Props> = ({ children, ...props }) => {
  const [isPopUpVisible, setIsPopUpVisible] = useState(false);
  const popUp = document.getElementById('popup');

  const logoutHandler = () => {
    setIsPopUpVisible(true);
  };

  return (
    <>
      <Button onClick={logoutHandler} {...props}>
        {children}
      </Button>
      {!!popUp &&
        isPopUpVisible &&
        createPortal(
          <ExitPopup onClose={() => setIsPopUpVisible(false)} />,
          popUp,
        )}
    </>
  );
};
