import { type ComponentProps, FC, type ReactNode } from 'react';
import s from './secondaryWrapper.module.css';
import {
  ChevronDown,
  Close,
} from '@popup/project/youboost/components/shared/icons';
import clsx from 'clsx';

interface Props {
  header: ReactNode;
  children: ReactNode;
  footer?: ReactNode;
  backButtonProps?: Omit<ComponentProps<'button'>, 'onClick'> & {
    onClick: () => void | Promise<void>;
  };
  closeClickHandler?: () => void | Promise<void>;
}

export const SecondaryWrapper: FC<Props> = ({
  footer,
  children,
  header,
  backButtonProps,
  closeClickHandler,
}) => {
  return (
    <div className={s.wrapper}>
      <div className={s.header}>
        <div className={s.icon}>
          {backButtonProps && (
            <button
              {...backButtonProps}
              className={clsx(s.action, s.icon, backButtonProps.className)}
              data-testid={'back-button'}
              data-close="close-button"
            >
              <ChevronDown className={s.backIcon} />
            </button>
          )}
        </div>
        <div className={s.middle}>{header}</div>
        {closeClickHandler && (
          <button
            onClick={closeClickHandler}
            className={clsx(s.action, s.icon)}
            data-testid={'close-button'}
            data-close="close-button"
          >
            <Close className={s.icon} />
          </button>
        )}
      </div>
      <div className={s.main}>{children}</div>
      {footer && <div className={s.footer}>{footer}</div>}
    </div>
  );
};
