import { clsx } from 'clsx';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

import { FreeInfoPersuasionAdvantages } from '@popup/project/youboost/components/FreeInfoPersuasionAdvantages';
import { SecondaryWrapper } from '@popup/project/youboost/components/shared/SecondaryWrapper';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { type CancelPage } from '../../types/CancelPage';
import { FooterButtons } from './FooterButtons/FooterButtons';

import styles from './FreeRestrictions.module.css';

type FreeRestrictionsProps = {
  page: CancelPage;
  onNext: () => void;
};

export function FreeRestrictions({
  onNext,
  page,
}: FreeRestrictionsProps): JSX.Element | null {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.free-restrictions',
  });

  const navigate = useNavigate();

  return (
    <div
      className={clsx(styles.wrapper, {
        [styles['out-right']]: page === 'problem',
        [styles['out-left']]: page === 'discount',
        [styles['out-delay']]: page !== 'discount',
      })}
      data-attribute="freeRestrictions"
    >
      <SecondaryWrapper
        header={<h1 className={styles.title}>{t('title')}</h1>}
        footer={<FooterButtons onNext={onNext} />}
        closeClickHandler={() => navigate(RoutePath.Root)}
      >
        <FreeInfoPersuasionAdvantages className={styles.advantages} />
      </SecondaryWrapper>
    </div>
  );
}
