import { FC } from 'react';
import s from './PremiumData.module.css';
import { Tag } from '@popup/project/youboost/components/shared/Tag/ui/Tag';
import { usePremiumData } from '@popup/shared/hooks/usePremiumData';
import { LeftTime } from '@popup/project/youboost/components/SubscriptionManagementContent/ui/LeftTime/LeftTime';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { useTranslation } from 'react-i18next';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';
import { useLocation, useNavigate } from 'react-router-dom';
import { usePaymentStatus } from '@popup/shared/hooks/usePaymentStatus';
import { CancelRenewalRow } from '@popup/project/youboost/components/SubscriptionManagementContent/ui/CancelRenewalRow/CancelRenewalRow';
import { useFeaturesList } from '@app/features/hooks/useFeaturesList';
import { Feature } from '@app/features/types/Feature';

interface Props {}

export const PremiumData: FC<Props> = ({}) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { premiumDaysLeft, secondsLeft, paidUntil, userId, isAutoRenew } =
    usePremiumData();
  const paymentStatus = usePaymentStatus();
  const { featuresList } = useFeaturesList();
  const isAutoRenewalFeatureEnabled = featuresList[Feature.AutoRenewal];

  const { t } = useTranslation('youboost', {
    keyPrefix: 'subscription-management-page.premium',
  });

  if (
    typeof premiumDaysLeft === 'undefined' ||
    typeof secondsLeft === 'undefined' ||
    typeof userId === 'undefined'
  )
    return null;

  const extendHandler = () => {
    navigate(RoutePath.Tariffs, {
      state: { prevRoute: location.pathname },
    });
  };

  const refundHandler = () => {
    navigate(RoutePath.CancelPremium);
  };
  const showRefundBtn = paymentStatus === 'Paid' && premiumDaysLeft > 1;

  return (
    <div className={s.box}>
      <div className={s.top}>
        <div className={s.row}>
          <LeftTime
            paidUntil={paidUntil}
            daysLeft={premiumDaysLeft}
            secondsLeft={secondsLeft}
          />
          <Tag>Premium</Tag>
        </div>
        {isAutoRenewalFeatureEnabled && isAutoRenew && (
          <CancelRenewalRow userId={userId} />
        )}
      </div>
      <div className={s.actions}>
        <Button size={'default'} color={'primary'} onClick={extendHandler}>
          {t('extend')}
        </Button>
        {showRefundBtn && (
          <Button size={'default'} color={'tertiary'} onClick={refundHandler}>
            {t('refund')}
          </Button>
        )}
      </div>
    </div>
  );
};
