import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Error } from '@popup/project/youboost/components/shared/errors/Error';
import { useCancelPremium } from '@popup/shared/hooks/useCancelPremium';
import { usePremiumAnimation } from '@popup/shared/hooks/usePremiumAnimation';
import {
  Discount,
  FreeRestrictions,
  Problem,
  type CancelPage,
} from './components';
import { Success } from './components/Success';
import { LoadingScreen } from '../../shared/LoadingScreen/LoadingScreen';

export function CancelPremiumContent(): JSX.Element | null {
  const [page, setPage] = useState<CancelPage>('problem');
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium',
  });

  usePremiumAnimation();

  const {
    isSuccess,
    cancelStatus,
    errorMessage,
    isCancelPremiumError,
    isCancelPremiumLoading,
    handleCancelPremium,
  } = useCancelPremium();

  useEffect(() => {
    if (isSuccess) {
      setPage('success');
    }
  }, [isSuccess]);

  if (isCancelPremiumLoading) {
    return (
      <LoadingScreen
        title={t('loader-text.title')}
        text={t('loader-text.status-paid')}
      />
    );
  }

  if (isCancelPremiumError) {
    return <Error text={errorMessage} />;
  }

  return (
    <>
      <Problem
        page={page}
        onNext={() => {
          setPage('free-restrictions');
        }}
      />
      <FreeRestrictions
        page={page}
        onNext={() => {
          setPage('discount');
        }}
      />
      <Discount page={page} handleCancelPremium={handleCancelPremium} />
      <Success page={page} cancelStatus={cancelStatus} />
    </>
  );
}
