import { useTranslation } from 'react-i18next';
import { browser_api } from '@app/browser_api';
import { SecondaryWrapper } from '../../../SecondaryWrapper/ui/SecondaryWrapper';
import { Title } from './Title';
import { MainSection } from './MainSection';
import { WarningIcon } from './icons';
import { Footer } from './Footer';
import { useCallback } from 'react';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type ErrorProps = {
  title?: string | null;
  icon?: React.ReactNode;
  subtitle?: string | null;
  text?: React.ReactNode;
  textLink?: React.ReactNode;
  code?: number;
  footer?: React.ReactNode;
  children?: React.ReactNode;
  onClose?: () => void;
};

export function Error({
  title,
  icon,
  subtitle,
  text,
  textLink,
  code,
  footer,
  children,
  onClose,
}: ErrorProps): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'error.default',
  });

  const defaultTitle = t('title');
  const defaultSubtitle = t('subtitle');
  const defaultText = t('text');

  const handleClose = useCallback(() => {
    if (onClose) {
      onClose();
    } else {
      setLocalStorageData({ [LocalStorageKey.ShouldOpenPopup]: true }).finally(
        () => {
          browser_api.runtime.reload();
        },
      );
    }
  }, [onClose]);

  return (
    <SecondaryWrapper
      header={<Title title={title || defaultTitle} />}
      footer={footer || <Footer />}
      closeClickHandler={handleClose}
    >
      <MainSection
        icon={icon || <WarningIcon />}
        subtitle={subtitle || defaultSubtitle}
        text={text ?? defaultText}
        code={code}
        textLink={textLink}
        children={children}
      />
    </SecondaryWrapper>
  );
}
