import { useTranslation } from 'react-i18next';
import { Button } from '@popup/project/youboost/components/shared/Button';
import styles from './FooterButtons.module.css';
import { useNavigate } from 'react-router-dom';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

export const FooterButtons = ({
  onNext,
}: {
  onNext: () => void;
}): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'cancel-premium.free-restrictions.actions',
  });

  const navigate = useNavigate();

  return (
    <div className={styles.buttons}>
      <Button
        color="primary"
        size="default"
        onClick={() => navigate(RoutePath.Root)}
      >
        {t('stay-premium')}
      </Button>
      <Button color="tertiary" size="default" onClick={onNext}>
        {t('continue')}
      </Button>
    </div>
  );
};
