import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { Button } from '@popup/project/youboost/components/shared/Button';
import { RoutePath } from '@popup/project/youboost/routes/types/RoutePath';

import styles from './FooterButtons.module.css';

export const FooterButtons = (): React.ReactNode => {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'all-internet-promo.actions',
  });

  const navigate = useNavigate();

  return (
    <div className={styles.buttons}>
      <Button
        color="primary"
        size="big"
        onClick={() => navigate(RoutePath.Tariffs)}
      >
        {t('continue')}
      </Button>
      <Button
        color="tertiary"
        size="default"
        onClick={() => navigate(RoutePath.Root)}
      >
        {t('go-back')}
      </Button>
    </div>
  );
};
