import { Button } from '@popup/project/youboost/components/shared/Button';
import { useTranslation } from 'react-i18next';
import styles from './DebugButton.module.css';
import clsx from 'clsx';
import { ButtonProps } from '../../Button/types';
import { useDebugButton } from '@popup/project/youboost/components/SettingsContent/hooks/useDebugButton';

type DebugButtonProps = {
  text?: JSX.Element;
  className?: string;
  buttonColor?: ButtonProps['color'];
  buttonSize?: ButtonProps['size'];
};

export function DebugButton({
  text,
  className,
  buttonColor = 'tertiary',
  buttonSize = 'default',
}: DebugButtonProps): JSX.Element {
  const { t } = useTranslation('youboost', {
    keyPrefix: 'debug-button',
  });
  const { handleDownload, isDownloaded } = useDebugButton();

  return (
    <Button
      color={buttonColor}
      size={buttonSize}
      onClick={handleDownload}
      className={clsx(styles.debug, className)}
    >
      <div className={clsx(styles.info, { [styles.copied]: isDownloaded })}>
        {text || <span>{t('text')}</span>}
      </div>
      <span
        className={clsx(styles['copy-text'], {
          [styles.copied]: isDownloaded,
        })}
      >
        {t('copied')}
      </span>
    </Button>
  );
}
